import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';

import BonusModal from './bonus-modal';
import BonusShakeMoney from './bonus-shake-money';
import { DocumentTitle, WechatConfig, checkUserAuth } from '../../components';
import appConfig from '../../config/app-config';
import { connBasicLayout } from '../layout';
import { bonusService, resourceService } from '../../service';
import { message, tools, monitor } from '../../utils';
import { STUDENT_TYPE } from '../../constants';

import styles from './styles.less';

const { basename } = appConfig;

// 
const BONUS_IMAGE_STATUS = {
  CHECK: '',
  FINISH: '',
  UNFINISH: ''
};
const miniWxToken = tools.getMiniWxToken();
const bookingUrl = miniWxToken ? `/minibooking?miniWxToken=${miniWxToken}` : '/booking';
const forwardingUrl = miniWxToken ? `/forwarding?miniWxToken=${miniWxToken}` : '/forwarding';
// 
const MODAL_TYPE = {
  RecommendToTrialClass: {
    modalKey: 'tip',
    modalData: {
      bouns: 200
    }
  },
  RecommendToTrialClass_after_1910: {
    modalKey: 'tip',
    modalData: {
      bouns: 500
    }
  },
  // +300
  ShareTimelineMessage: {
    modalKey: 'notice',
    modalData: {
      title:
        '300~',
      desc: [
        '1+',
        '21300',
        '31~',
        '4/'
      ]
    }
  },
  // +1000
  MonthShareTimeMessage: {
    modalKey: 'notice',
    modalData: {
      title:
        '1000~',
      desc: [
        '1+',
        '211000',
        '31~',
        '4/'
      ]
    }
  },
  // +300 -- 1910
  MonthShareTimeMessage_after_1910: {
    modalKey: 'notice',
    modalData: {
      title:
        '300~',
      desc: [
        '1+',
        '21300',
        '31~',
        '4/'
      ]
    }
  },
  HardLearning: {
    modalKey: 'jump'
  },
  VeryHardLearning: {
    modalKey: 'jump'
  },
  RECOMMEND_TO_INTERNALCOURSR: {
    modalKey: 'notice',
    modalData: {
      title: '3000~',
      desc: [
        '1',
        '230'
      ]
    }
  },
  RECOMMEND_TO_TRAINCOURSR: {
    modalKey: 'notice',
    modalData: {
      title: '3000~',
      desc: [
        '1',
        '230'
      ]
    }
  },
  RecommendToShopping: {
    modalKey: 'notice',
    modalData: {
      title:
        '10000~',
      desc: [
        '1',
        '230'
      ]
    }
  }
};

@connBasicLayout({ useFooter: true, backgdName: '' })
@checkUserAuth({ support: 'all', mode: 'base' })
export default class Bonus extends PureComponent {
  state = {
    modalKey: '', //  notice/tip/error
    accessList: [], // 
    bonusPoint: 0, // 
    modalData: {}, // 
    bonusImgUrl: '', // 
    isShowShakeMoney: false // 
  };

  componentDidMount() {
    // 
    this.loadAccessList();
  }

  /**
   * 
   */
  loadAccessList = async () => {
    message.showLoading();
    try {
      // 
      const getBonusPoint = bonusService.getBonusCount;
      // 
      const getBonusAccessList = bonusService.getBonusAccessList;
      // 
      const getBonusUrl = resourceService.getBonusUrl;
      const [
        { balance = 0 },
        accessList,
        { backgroundUrl, magicLabel, haveGetBonus, rule, haveReadRule, haveReadRuleNew }
      ] = await Promise.all([
        getBonusPoint().catch((e) => {
          console.log(e);
        }),
        getBonusAccessList().catch((e) => {
          console.log(e);
        }),
        getBonusUrl().catch((e) => {
          console.log(e);
        })
      ]);
      let modalKey = this.state.modalKey;
      console.log('msg', haveReadRule, magicLabel, haveGetBonus);
      // 1910
      if (!haveReadRule && magicLabel === STUDENT_TYPE.OCT_AFTER && haveGetBonus) {
        modalKey = 'readRule';
        document.body.style.position = 'fixed';
        document.body.style.top = 0;
      }
      // 19101123
      if (haveReadRuleNew) {
        modalKey = 'readRuleNew';
        // ruleText = '<p><span style="font-size:12px"><span style="border-left:5px solid #c46aaf;padding-left:8px"></span></span></p><p><span style="font-size:12px">300</span></p><p></p><p><span style="font-size:12px"><span style="border-left:5px solid #c46aaf;padding-left:8px"></span>2</span></p><p><span style="font-size:12px">1. 1-15300</span></p><p><span style="font-size:12px">2. 16-300</span></p><p></p><p><span style="font-size:12px"><span style="border-left:5px solid #c46aaf;padding-left:8px"></span>2019121</span></p>';
        document.body.style.position = 'fixed';
        document.body.style.top = 0;
      }

      this.setState({
        bonusPoint: balance,
        accessList,
        bonusImgUrl: backgroundUrl.imgUrl,
        studentTYpe: magicLabel,
        haveGetBonus,
        modalKey,
        modalData: { rule },
        isShowShakeMoney: tools.getQueryString('complete') ? true : false
      });
      message.closeLoading();
    } catch (e) {
      message.closeLoading();
      console.log(e);
    }
  };

  // 
  readBounsRule = () => {
    bonusService.handleReadRule().then((data) => {
      monitor.log(data, ``);
      this.onHideModal();
      document.body.style.position = 'relative';
    });
  };

  // 
  readBonusRuleNew = () => {
    bonusService.handleReadRule().then((data) => {
      monitor.log(data, ``);
      this.onHideModal();
      document.body.style.position = 'relative';
    });
  };

  /**
   * 
   * @param modalKey 
   * @param finish 
   * @param processDone 
   * @param processInvalid 
   */
  onShowModal = (type, processDone, processInvalid) => {
    const { modalKey, modalData } = MODAL_TYPE[type];
    if (modalKey === 'jump') {
      //  
      if (processDone === 0 && processInvalid === 0) {
        this.props.history.push(bookingUrl);
      } else {
        const bonus_course = miniWxToken
          ? `/bonus_course/${type}?miniWxToken=${miniWxToken}`
          : `/bonus_course/${type}`;
        this.props.history.push(bonus_course);
      }
      return;
    } else if (modalKey === 'jump') {
      return;
    }

    this.setState({
      modalKey,
      modalData
    });
  };

  /**
   * 
   */
  shakeMoneyEnd = () => {
    this.setState({
      isShowShakeMoney: false
    });
  };

  /**
   * 
   */
  onHideModal = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * 
   */
  handleBtnClick = () => {
    this.props.history.push(forwardingUrl);
  };

  renderGetCoinMethod = () => {
    const { accessList } = this.state;

    return (
      <div className={styles.bonusGetMethod}>
        {accessList &&
          accessList.map((item) => {
            // , 
            // MonthShareTimeMessage: 1910  
            // MonthShareTimeMessage_after_1910: 1910  
            const isMonthShare =
              item.type === 'MonthShareTimeMessage' ||
              item.type === 'MonthShareTimeMessage_after_1910';
            return (
              <div className={styles.bonusMethodItem} key={item.type}>
                <div className={styles.itemLeft}>
                  <div className={styles.itemLeftTitle}>
                    <span className={styles.title}>{item.name}</span>
                    <span className={styles.coin} />
                    <span className={styles.coinNum}>+{item.magicCoinAmount}</span>
                  </div>
                  {item.desc && <div className={styles.itemLeftDesc}>{item.desc}</div>}
                  {(item.totalDone || item.totalDone === 0) && (
                    <div className={styles.itemLeftDesc}>
                      {(item.totalDone || item.totalDone === 0) && `${item.totalDone}`}
                    </div>
                  )}
                  {item.processTarget && item.processTarget !== item.processDone && (
                    <div className={styles.itemProgress}>
                      <div className={styles.progressBar}>
                        <div
                          className={styles.progress}
                          style={{ width: `${(item.processDone / item.processTarget) * 100}%` }}
                        />
                      </div>
                      <div className={styles.progressNum}>
                        {item.processDone}/{item.processTarget}
                      </div>
                    </div>
                  )}
                </div>
                <div className={styles.itemRight}>
                  {isMonthShare ? (
                    <div className={styles.earnCoin}>
                      <a href={basename + forwardingUrl}>{BONUS_IMAGE_STATUS[item.finish]}</a>
                    </div>
                  ) : (
                    <div
                      className={styles.earnCoin}
                      onClick={this.onShowModal.bind(
                        null,
                        item.type,
                        item.processDone,
                        item.processInvalid
                      )}
                    >
                      {BONUS_IMAGE_STATUS[item.finish]}
                    </div>
                  )}
                </div>
              </div>
            );
          })}
      </div>
    );
  };

  render() {
    const { modalKey, bonusPoint, modalData, bonusImgUrl, isShowShakeMoney } = this.state;
    // 
    if (!bonusImgUrl) return null;

    if (isShowShakeMoney) {
      document.body.style.cssText += 'position:fixed;width:100%;top:0px;';
    } else {
      document.body.style.position = 'static';
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      document.body.style.top = '';
    }

    return (
      <div className={styles.bonusWrapper}>
        {isShowShakeMoney && <BonusShakeMoney onShakeMoneyEnd={this.shakeMoneyEnd} />}
        <div className={styles.bonusTop}>
          <div className={styles.bonusTopImg}>
            <div />
          </div>
          <div className={styles.bonusTitle}>
            <span className={styles.bonusTitleNum}>{bonusPoint}</span>
            <Link to="/logs">
              <span className={styles.bonusTitleLink}></span>
            </Link>
          </div>
        </div>
        {this.renderGetCoinMethod()}
        <div className={styles.bonusBottom}>
          <img src={bonusImgUrl} />
        </div>
        {modalKey && !isShowShakeMoney && (
          <BonusModal
            modalKey={modalKey}
            onHideModal={this.onHideModal}
            onOk={this.readBounsRule}
            modalData={modalData}
            onNewRuleOk={this.readBonusRuleNew}
          />
        )}

        <DocumentTitle title="" />
        <WechatConfig showShare={false} />
      </div>
    );
  }
}
