import React, {Component} from 'react';
import {inject, observer} from 'mobx-react';

import styles from './index.less';

import Upload from './upload';

// 
@inject(({forwardingStore}) => {
    return {
        modal: forwardingStore.state.modal, // 
        progress: forwardingStore.state.progress, // 
    };
})
@observer
export default class ForwardingModal extends Component {
    // close
    handleCloseClick = () => {
        let {onCancel} = this.props;
        onCancel && onCancel();
    }

    // 
    handleSubmit = () => {
        let {onOk} = this.props;
        onOk && onOk();
    }

    // 
    modalShow = () => {
        let {modal, progress} = this.props;
        let content = null;
        switch (modal) {
            case 'uploading':
                content = (
                    <div className={styles.smallBox}>
                        <div className={styles.outBar}>
                            <div className={styles.innerBar} style={{right: `${100 - progress}%`}}></div>
                        </div>
                        <div className={styles.uploadProgress}>
                            ...{progress}%
                        </div>
                    </div>
                );
                break;
            case 'success':
                content = (
                    <div className={styles.bigBox}>
                        <div className={styles.content}>
                            <div className={styles.successLogo}/>
                            <div className={styles.text}>
                                <div className={styles.title}></div>
                                <div className={styles.letter}>3</div>
                                <div className={styles.letter}></div>
                                <div className={styles.btn} onClick={this.handleSubmit}></div>
                            </div>
                        </div>
                        <div className={styles.close} onClick={this.handleCloseClick}></div>
                    </div>
                );
                break;
            case 'fail':
                content = (
                    <div className={styles.bigBox}>
                        <div className={styles.content}>
                            <div className={styles.failLogo}/>
                            <div className={styles.text}>
                                <div className={styles.letter}></div>
                                <Upload>
                                    <div className={styles.btn}></div>
                                </Upload>
                            </div>
                        </div>
                        <div className={styles.close} onClick={this.handleCloseClick}></div>
                    </div>
                );
                break;
            default:
                content = null;
        }
        return (
            <div className={styles.modalBox}>{content}</div>
        );
    }

    render() {
        return (
            <div>
                {this.modalShow()}
            </div>
        );
    }
}