/**
 * 
 */
class BonusErrorModal extends PureComponent {
  render() {
    const {
      modalData: { courseType }
    } = this.props;
    let text = (
      <p>
        
        <span className={styles.course}>{courseType}</span>
        ~
      </p>
    );
    if (courseType === '') {
      text = (
        <p>
          
          <span className={styles.course}>{courseType}</span>
          ~
        </p>
      );
    }

    return (
      <div className={styles.bonusErrorModal}>
        <div className={styles.errorLogo} />
        <div className={styles.content}>{text}</div>
      </div>
    );
  }
}

/**
 * 
 */
class BonusNoticeModal extends PureComponent {
  render() {
    const {
      modalData: { title, desc }
    } = this.props;
    return (
      <div className={styles.bonusNoticeModal}>
        <div className={styles.top}>{title}</div>
        <div className={styles.content}>
          <div className={styles.title}></div>
          <div className={styles.desc}>{desc && desc.map((item) => <p key={item}>{item}</p>)}</div>
        </div>
      </div>
    );
  }
}

/**
 * 
 */
export default class BonusModal extends PureComponent {
  static propTypes = {
    modalKey: PropTypes.string, // 
    modalData: PropTypes.object, // 
    onHideModal: PropTypes.func, // 
    onOk: PropTypes.func
  };

  static defaultProps = {
    modalKey: '',
    modalData: {},
    onHideModal: () => {
      console.log('');
    },
    onOk: () => {}
  };

  preventDefault = (e) => {
    e.preventDefault();
  };

  handleHideModal = () => {
    const { onHideModal } = this.props;
    onHideModal && onHideModal();
  };

  renderContent = () => {
    const { modalKey, modalData, onOk, onNewRuleOk } = this.props;
    let modal = null;
    let showClose = true;
    switch (modalKey) {
      case 'notice':
        modal = <BonusNoticeModal onHideModal={this.handleHideModal} modalData={modalData} />;
        break;
      case 'error':
        modal = <BonusErrorModal onHideModal={this.handleHideModal} modalData={modalData} />;
        break;
      case 'tip':
        modal = <BonusTipModal onHideModal={this.handleHideModal} modalData={modalData} />;
        break;
      case 'readRule':
        modal = (
          <BonusUseModal onHideModal={this.handleHideModal} modalData={modalData} onRead={onOk} />
        );
        showClose = false;
        break;
      case 'readRuleNew':
        modal = <BonusUseNewModal onHideModal={this.handleHideModal} onRead={onNewRuleOk} />;
        showClose = false;
        break;
    }

    return (
      <div>
        <div className={styles.bonusModalMask} onTouchMove={this.preventDefault} />
        <div className={styles.bonusModalContent}>
          {modal}
          {showClose && <div className={styles.bonusModalClose} onClick={this.handleHideModal} />}
        </div>
      </div>
    );
  };

  render() {
    return <div className={styles.bonusModal}>{this.renderContent()}</div>;
  }
}
