import React from 'react';
import {Card, WhiteSpace} from 'antd-mobile';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import styles from './bonusLog.less';

// 
const BonusHeader = (props) => {
    const {bonusTotal, bonusFreeze, onClick} = props;

    /**
     * 
     */
    const renderHeaderTitle = () => (
        <div className={classNames(styles['header'])}>
            <span className={classNames(styles['title'])}></span>
            <span className={classNames(styles['subtitle'])}>()</span>
        </div>
    );

    /**
     * 
     */
    const renderFooterContent = () => (
        <div className={classNames(styles['footer'])}>
            <span className={classNames(styles['footer-warn'])}></span>
            <span className={classNames(styles['footer-title'])}>{bonusFreeze}</span>
            <span className={classNames(styles['footer-go'])}></span>
        </div>
    );

    return (
        <div className={classNames(styles['bonus-log-header'])}>
            <WhiteSpace/>
            <Card full>
                <Card.Header
                    title={renderHeaderTitle()}
                />
                <Card.Body>
                    <div className={classNames(styles['bonus-total'])}>{bonusTotal}</div>
                </Card.Body>
                <Card.Footer content={renderFooterContent()} onClick={onClick}/>
            </Card>
        </div>
    );
};

// 
BonusHeader.propTypes = {
    bonusTotal: PropTypes.number, // 
    bonusFreeze: PropTypes.number, // 
    onClick: PropTypes.func.isRequired, // 
};

// 
BonusHeader.defaultProps = {
    bonusTotal: 0, // 0
    bonusFreeze: 0, // 0
};

export default BonusHeader;
