/**
 * 
 * @param tradeType 
 * @param tradeDate 
 */
export const getBonusLogList = ({ tradeType, tradeDate, pageIndex, pageSize }) => {
  const url = baseDomain.concat('/usercenter/magicCoin/logs');

  let [type] = tradeType.slice();
  type = type === 'ALL' ? undefined : type;

  const param = {
    tradeType: type,
    tradeDate: `${tradeDate}${tradeDate && '-01'}`,
    pageIndex,
    pageSize
  };

  return fetchUtils.get(url, param).then((res) => res.data);
};

