import React, { Component } from 'react';
import classNames from 'classnames';

import defaultPhotoUrl from '../../public/images/home/default-photo.png';
import coinUrl from '../../public/images/home/coin.png';
import posterUrl from '../../public/images/home/poster.png';
import arrowUrl from '../../public/images/home/arrow.png';
import newUrl from '../../public/images/home/new.png';

import { connBasicLayout } from '../layout';
import Footer from '../layout/footer';
import { DocumentTitle, WechatConfig, InfoModal, checkUserAuth } from '../../components';

import { userService } from '../../service';
import { localStore, message, monitor } from '../../utils';
import { appConfig } from '../../config';
import styles from './home.less';

// 
const { protocolNewUrl, questionUrl, levelTestUrl, myOrderUrl } = appConfig.resources;
// 
const basename = appConfig.basename;
const promotionPath = `${basename}/promotion`; // 
const remindPath = `${basename}/remind`; // 
const bonusPath = `${basename}/bonus`; // 

/**
 * 
 * @type {{title: string}}
 */
const shareOptions = {
  title: '-'
};

/**
 * 
 */
@connBasicLayout({ useFooter: true })
@checkUserAuth({ support: 'all', mode: 'base' })
class Home extends Component {
  state = {
    quantity: false, // 
    userInfo: {},
    skillLevelBubble: false,
    inRankWhiteList: false,
    orderMask: false // 
  };

  componentDidMount() {
    // let isMiniApp = localStore.get('isMiniApp');
    // if (isMiniApp === 'true') {
    //     this.setState({
    //         isMiniApp: true
    //     });
    // } else {
    //     this.setState({
    //         isMiniApp: false
    //     });
    // }
    this.initData();
  }

  /**
   * 
   */
  initData = () => {
    message.showLoading();
    Promise.all([userService.getUserBaseInfo(), this.getSkillLevelBubble(), this.getScaleByUser()])
      .then(([userInfo, { skillLevelBubble, inRankWhiteList }, quantity]) => {
        this.setState({
          userInfo,
          skillLevelBubble,
          inRankWhiteList,
          quantity
        });
        if (quantity && !localStore.get('orderGuide')) {
          this.setState({
            orderMask: true
          });
        }
        message.closeLoading();
      })
      .catch((error) => {
        message.closeLoading();
        message.error(error.msg || '');
        this.setState({
          quantity: false
        });
      });
  };

  /**
   * 
   */
  getScaleByUser = () => {
    return userService.scaleByUser().catch(() => {
      return false;
    });
  };

  getSkillLevelBubble = () => {
    return userService.getUserSpecialNumber().catch(() => {
      // 
      // 
      return { skillLevelBubble: false, inRankWhiteList: false };
    });
  };

  /**
   * href
   */
  handleHrefChange = (url) => {
    return () => {
      window.location.href = url;
    };
  };

  /**
   * 
   */
  handleLogout = () => {
    const url = `${basename}/login`;

    userService
      .logout()
      .then(() => {
        localStore.clearToken();
        // rf
        localStore.remove('rf');
        monitor.logout();
        window.location.replace(url);
      })
      .catch((error) => {
        message.error(error.msg || '');
      });
  };

  handleShowModal = (key) => {
    return () => {
      this.setState({
        modalKey: key
      });
    };
  };

  /**
   * modal
   */
  handleHideModal = () => {
    this.setState({
      modalKey: ''
    });
  };

  renderModal = () => {
    const { modalKey } = this.state;
    let element = null;
    switch (modalKey) {
      case 'logout':
        element = (
          <InfoModal headerType="weep" onCancel={this.handleHideModal} onOk={this.handleLogout}>
            <div className={styles.modalCentent}>
              
              <p></p>
            </div>
          </InfoModal>
        );
        break;
    }
    return element;
  };

  /**
   * 
   */
  handleCloseTip = (ev) => {
    ev.stopPropagation();
    localStore.set('orderGuide', 'true');
    this.setState({
      orderMask: false
    });
  };

  render() {
    const { userInfo, skillLevelBubble, inRankWhiteList, quantity, orderMask } = this.state;
    return (
      <div className={styles.homeBox}>
        <DocumentTitle title="" />
        <div className={styles.header}>
          <div className={classNames(styles.user, 'clearfix')}>
            <img src={defaultPhotoUrl} alt="" className={styles.photo} />
            <div className={styles.userInfo}>
              <div className={styles.userName}>{userInfo['nikeName'] || userInfo['name']}</div>
              <div className={classNames(styles.userProps, 'clearfix')}>
                <span>Level {userInfo['level']}</span>
                <span>: {userInfo['studentId']}</span>
              </div>
            </div>
          </div>
        </div>
        <div className={classNames(styles.period, 'clearfix')}>
          <div>
            <p>{userInfo['balance'] || 0}</p>
            <p></p>
          </div>
          <div>
            <p>{userInfo['spBalance'] || 0}</p>
            <p></p>
          </div>
          <div>
            <p>{userInfo['consume'] || 0}</p>
            <p></p>
          </div>
          <div>
            <p>{userInfo['appoint'] || 0}</p>
            <p></p>
          </div>
        </div>
        <div className={classNames(styles.sider, 'clearfix')}>
          {/* <div onClick={this.handleHrefChange(campUrl)}>
                        <p>
                            <img src={trainUrl} alt=""/>
                        </p>
                        <p></p>
                    </div> */}
          <div onClick={this.handleHrefChange(bonusPath)}>
            <p>
              <img src={coinUrl} alt="" />
            </p>
            <p></p>
          </div>
          <div>
            <p onClick={this.handleHrefChange(promotionPath)}>
              <a>
                <img src={posterUrl} alt="" />
              </a>
              <img src={newUrl} alt="" className={styles.new} />
            </p>
            <p>
              <a onClick={this.handleHrefChange(promotionPath)}></a>
            </p>
          </div>
        </div>
        <div className={styles.section}>
          {quantity ? (
            <div className={styles.order} onClick={this.handleHrefChange(myOrderUrl)}>
              <img src={arrowUrl} alt="" />
              <div></div>
              {orderMask ? (
                <span className={styles.orderMask} onClick={this.handleCloseTip}>
                  <i className={styles.maskIcon}></i>
                  <em className={styles.maskFont}></em>
                  <span className={styles.guide}></span>
                </span>
              ) : null}
            </div>
          ) : (
            <div className={styles.protocol} onClick={this.handleHrefChange(protocolNewUrl)}>
              <img src={arrowUrl} alt="" />
              <div></div>
            </div>
          )}
          <div className={styles.remind} onClick={this.handleHrefChange(remindPath)}>
            <img src={arrowUrl} alt="" />
            <div></div>
          </div>
          {inRankWhiteList && (
            <div className={styles.levelTest} onClick={this.handleHrefChange(levelTestUrl)}>
              <img src={arrowUrl} alt="" />
              <div>
                
                {skillLevelBubble && <div className={styles.new} />}
              </div>
            </div>
          )}
          <div className={styles.question} onClick={this.handleHrefChange(questionUrl)}>
            <img src={arrowUrl} alt="" />
            <div></div>
          </div>
        </div>
        <div className={styles.logoutMask}>
          <div className={styles.logout} onClick={this.handleShowModal('logout')}>
            
          </div>
        </div>
        {orderMask ? <div className={styles.maskLayer}></div> : null}
        {this.renderModal()}
        {/* <div style={{visibility: (isMiniApp) ? 'hidden' : 'visible'}}>
                    <Footer/>
                </div> */}
        <Footer />
        <WechatConfig showShare={true} shareOptions={shareOptions} />
      </div>
    );
  }
}

export default Home;
