import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import WX from 'weixin-js-sdk';

import styles from './index.less';

import appConfig from '../../config/app-config';
import { message, wechatUtils, monitor, tools } from '../../utils';
import { forwardingService } from '../../service';

// 
const options = {
  url: appConfig.domain.baseDomain + '/activity/file/image/upload',
  timeOut: 20000,
  header: {
    // 'Content-Type': 'application/json; charset=UTF-8',
  }
};

// 
@inject(({ forwardingStore }) => {
  return {
    // 
    progress: forwardingStore.state.progress, // 
    // 
    showModal: forwardingStore.showModal, // 
    modifyProgress: forwardingStore.modifyProgress, // 
    initData: forwardingStore.initData // 
  };
})
@observer
export default class Upload extends Component {
  state = {
    modal: false
  };

  // 
  refuseHandle = () => {
    this.setState({
      modal: true
    });
    return new Promise((resolve) => {
      this.handleCheck = resolve;
    });
  };

  createModal = () => {
    const { modal } = this.state;
    return modal ? (
      <div className={styles.uploadModal}>
        <div className={styles.Modalcontent}>
          <div className={styles.successLogo} />
          <div className={styles.title}></div>
          <div className={styles.btns}>
            <div onClick={this.handleModalClick.bind(null, false)} className={styles.btn}>
              
            </div>
            <div onClick={this.handleModalClick.bind(null, true)} className={styles.btn}>
              
            </div>
          </div>
        </div>
      </div>
    ) : null;
  };

  handleModalClick = (isConfirm) => {
    this.setState({
      modal: false
    });
    this.handleCheck({
      isConfirm
    });
  };

  // fileonChange
  uploadChange = async (event) => {
    const { showModal, initData, modifyProgress } = this.props;
    event.persist();
    const file = event.target.files[0];
    console.log(event.target.files, file);
    if (!file) return;
    monitor.log('', `-`);
    const { isConfirm } = await this.refuseHandle();
    // 
    if (!isConfirm) {
      event.target.value = ''; // eslint-disable-line
      return;
    }
    const formData = new FormData();
    formData.append('file', file);
    const xhr = new XMLHttpRequest();
    // 
    xhr.open('POST', options.url, true);
    // 
    if (options.header && Object.keys(options).length > 0) {
      for (const key in options.header) {
        if (options.header.hasOwnProperty(key)) {
          xhr.setRequestHeader(key, options.header[key]);
        }
      }
    }
    // 
    xhr.timeout = options.timeOut;

    // 
    xhr.addEventListener('loadstart', (e) => {
      showModal && showModal('uploading');
    });
    // 
    xhr.upload.addEventListener('progress', (e) => {
      modifyProgress && modifyProgress(Math.floor((e.loaded / e.total) * 100));
    });
    // 
    xhr.addEventListener(
      'load',
      (e) => {
        const response = JSON.parse(e.currentTarget.response);
        if (xhr.status === 200 && response.code === 'OK') {
          monitor.log('', `-${e.currentTarget.respons}`);
          forwardingService
            .uploadImage(response.data)
            .then((res) => {
              monitor.log('', `-${JSON.stringify(res)}`);
              message.showLoading();
              initData();
              showModal && showModal('success');
              event.target.value = '';
            })
            .catch((error) => {
              showModal && showModal('fail');
              event.target.value = '';
              monitor.log('', `-${JSON.stringify(error)}`);
            });
        } else {
          showModal && showModal('fail');
          event.target.value = '';
          monitor.log('', `-${xhr.status + '-' + response.code}`);
        }
      },
      false
    );
    // 
    xhr.addEventListener('timeout', (e) => {
      showModal && showModal('');
      message.error('');
      event.target.value = '';
      monitor.log('', `-${JSON.stringify(e)}`);
    });
    // 
    xhr.addEventListener(
      'error',
      (e) => {
        message.error('');
        showModal && showModal('');
        event.target.value = '';
        monitor.log('', `-${JSON.stringify(e)}`);
      },
      false
    );
    xhr.send(formData);
  };

  /**
   * 
   */
  beginUpload = () => {
    const { showModal, initData } = this.props;
    showModal('');
    monitor.log('', `-`);
    wechatUtils.chooseImage({
      callback(files) {
        monitor.log('', `-fiels${JSON.stringify(files)}`);
        message.showLoading('...');
        WX.getLocalImgData({
          localId: files[0],
          async success(res) {
            monitor.log('', `-res${JSON.stringify(res)}`);
            let localData = res.localData;
            // window.__wxjs_is_wkwebview
            if (localData.indexOf('data:image') !== -1) {
              // IOS
              localData = localData.replace('jpg', 'jpeg');
              monitor.log('', `--IOS`);
            } else {
              // 
              localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '');
              monitor.log('', `--`);
            }
            monitor.log(
              '',
              `-,base64:${localData.indexOf(
                'data:image/jpeg;base64'
              )}`
            );
            console.log('localData>>', localData);
            try {
              monitor.log('', `-`);
              const { url } = await forwardingService.choiceImageUpload(localData);
              if (url) {
                forwardingService
                  .uploadImage(url)
                  .then((res) => {
                    monitor.log('', `-`);
                    initData();
                    showModal && showModal('success');
                  })
                  .catch((error) => {
                    monitor.log('', `-error${JSON.stringify(error)}`);
                    console.log(error);
                    message.closeLoading();
                    if (error.code === 'BIZ_SHARE_TIMELINE_REPEAT_SUBMIT') return;
                    showModal && showModal('fail');
                  });
              } else {
                message.closeLoading();
                showModal && showModal('fail');
                monitor.log('', `-url url${url}`);
              }
            } catch (error) {
              message.closeLoading();
              console.log(error);
              showModal && showModal('fail');
              monitor.log('', `-error ${JSON.stringify(error)}`);
            }
          },
          fail(res) {
            monitor.log('', `error${JSON.stringify(res)}`);
            console.log(res);
            message.error('');
          }
        });
      }
    });
  };

  render() {
    return tools.isWeiXin() ? (
      <div className={styles.uploadWrapper} onClick={this.beginUpload}>
        {this.props.children}
        {this.createModal()}
      </div>
    ) : (
      <div className={styles.uploadWrapper}>
        {this.props.children}
        <input
          className={styles.uploadInput}
          type="file"
          accept="image/*"
          onChange={this.uploadChange}
        />
        {this.createModal()}
      </div>
    );
  }
}
