import OSS from 'ali-oss';

import {fetchUtils, tools, monitor} from '../utils';
import {appConfig} from '../config';

const {
    domain: {apiDomain, mobileDomain}
} = appConfig;
const bannerDomain = apiDomain.concat('/activity');

// 
const FILE_TYPE = {
    file: 'FILE',
    image: 'IMAGE',
    video: 'VIDEO',
    excel: 'EXCEL',
    audio: 'AUDIO'
};

/**
 * banner
 */
export const getBannersUrl = () => {
    const url = bannerDomain.concat('/config/search/banners');

    return fetchUtils.get(url).then(({data}) => {
        const {banners} = data;
        // banner 
        const bannersArray = banners.map(({sort, imageUrl, targetUrl}) => {
            return {
                key: Number(new Date()) + sort, // 
                link: targetUrl, // 
                imgUrl: imageUrl, // banner 
                sort // 
            };
        });

        // 
        bannersArray.sort((prev, next) => prev.sort - next.sort);

        return new Promise(resolve => {
            let count = 0;
            bannersArray.forEach(({imgUrl}) => {
                // 
                let img = new Image();
                img.src = imgUrl;
                img.onload = () => {
                    count++;
                    if (count === bannersArray.length) {
                        resolve(bannersArray);
                    }
                };
            });
        });
    });
};

/**
 * 
 */
export const getBonusUrl = () => {
    const url = apiDomain.concat('/usercenter/magicCoin/get/config');
    return fetchUtils.get(url).then(({data}) => {
        const {bgImgURL} = data;
        data.backgroundUrl = {
            key: Number(new Date()), // 
            imgUrl: bgImgURL // 
        };
        return data;
    });
};

