import React, { Component } from 'react';
import classNames from 'classnames';
import { WingBlank, WhiteSpace, ListView } from 'antd-mobile';
import moment from 'moment';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import BonusHeader from './bonusHeader';
import BonusSearch from './bonusSearch';
import BonusList from './bonusList';
import BonusModal from './bonusModal';

import { checkUserAuth, DocumentTitle, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';
import { message } from '../../utils';

import styles from './bonusLog.less';

// 
@inject(({ bonusStore }) => {
  return {
    bonusInfo: bonusStore.bonusInfo, // 
    freezePointList: bonusStore.freezePointList, // 
    getBonusPoint: bonusStore.getBonusPoint, //  func
    // getBonusFreezePoint: bonusStore.getBonusFreezePoint, //  func
    getBonusPointList: bonusStore.getBonusPointList, //  func
    getBonusFreezePointList: bonusStore.getBonusFreezePointList //  func
  };
})
@observer
@connBasicLayout({ useFooter: true, backgdName: '' })
@checkUserAuth({ support: 'all', mode: 'base' })
class BonusLog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataSource: new ListView.DataSource({ rowHasChanged: (r1, r2) => r1 !== r2 }), // 
      freezeDataSource: new ListView.DataSource({ rowHasChanged: (r1, r2) => r1 !== r2 }), // 
      isLoading: false, // loading
      dateTime: null, // 
      type: ['ALL'], // 
      isShow: false, // 
      index: 1, // 
      freezeIndex: 1, // 
      isFreezeLoading: false, // loading
      refreshing: false, // 
      freezeRefreshing: false, // 
      data: [], // 
      freezeData: [], // 
      total: 0, // 
      freezeTotal: 0 // 
    };
  }

  componentDidMount() {
    // 
    this.loadBonusPoint();

    // 
    // this.loadBonusFreezePoint();

    // 
    this.loadBonusPointList();
  }

  // 
  loadBonusPoint = () => {
    const { getBonusPoint } = this.props;
    getBonusPoint();
  };

  // 
  // loadBonusFreezePoint = () => {
  //     const {getBonusFreezePoint} = this.props;
  //     getBonusFreezePoint();
  // }

  /**
   * 
   */
  loadBonusPointList = () => {
    const { type: tradeType, dateTime, index, data } = this.state;

    // 
    const tradeDate = dateTime ? moment(dateTime).format('YYYY-MM') : '';
    const { getBonusPointList } = this.props;

    // loading
    if (index === 1) {
      message.showLoading();
    }
    getBonusPointList(tradeType, tradeDate, index)
      .then(({ data: res, total }) => {
        message.closeLoading();

        // 
        this.setState({
          isLoading: false,
          refreshing: false,
          index: index + 1,
          total: total,
          data: [...data, ...res],
          dataSource: this.state.dataSource.cloneWithRows([...data, ...res])
        });
      })
      .catch((e) => {
        message.closeLoading();
        this.setState({
          isLoading: false,
          refreshing: false
        });
        console.error(e);
      });
  };

  /**
   * 
   */
  loadBonusFreezePointList = () => {
    const { freezeIndex, freezeData } = this.state;

    const { getBonusFreezePointList } = this.props;

    // loading
    if (freezeIndex === 1) {
      message.showLoading();
    }
    // 
    getBonusFreezePointList(freezeIndex)
      .then(({ data: res, total }) => {
        message.closeLoading();

        const data = freezeIndex === 1 ? res : [...freezeData, ...res];
        // 
        this.setState({
          isFreezeLoading: false,
          freezeRefreshing: false,
          freezeIndex: freezeIndex + 1,
          freezeTotal: total,
          freezeData: data,
          freezeDataSource: this.state.freezeDataSource.cloneWithRows(data)
        });
      })
      .catch((e) => {
        message.closeLoading();
        this.setState({
          isFreezeLoading: false,
          freezeRefreshing: false
        });
        console.error(e);
      });
  };

  /**
   * 
   */
  debounceEndReached = () => {
    const { total, data } = this.state;
    if (total === data.length) {
      return false;
    }
    this.setState({ isLoading: true }, () => {
      this.loadBonusPointList();
    });
  };

  // 
  onEndReached = _.debounce(this.debounceEndReached, 500);

  /**
   * 
   */
  onRefresh = () => {
    this.setState({ refreshing: true, index: 1, data: [] }, () => {
      this.loadBonusPointList();
    });
  };

  /**
   * 
   */
  debounceFreezeRefresh = () => {
    this.setState({ freezeRefreshing: true, freezeIndex: 1, freezeData: [] }, () => {
      this.loadBonusFreezePointList();
    });
  };

  // 
  onFreezeRefresh = _.debounce(this.debounceFreezeRefresh, 500);

  /**
   * 
   */
  onFreezeEndReached = () => {
    const { freezeTotal, freezeData } = this.state;
    if (freezeTotal === freezeData.length) {
      return false;
    }
    this.setState({ isFreezeLoading: true }, () => {
      this.loadBonusFreezePointList();
    });
  };

  /**
   * 
   */
  handleClick = () => {
    const { isShow } = this.state;
    this.setState({ isShow: !isShow, freezeIndex: 1 });

    if (isShow) return;
    // 
    this.loadBonusFreezePointList();
  };

  /**
   * 
   */
  handleCancelClick = (val, field) => {
    //  
    this.setState({ [field]: val, data: [], index: 1 }, () => {
      // 
      this.loadBonusPointList();
    });
  };

  /**
   * 
   */
  handleOkClick = (val, field) => {
    //  
    this.setState({ [field]: val, data: [], index: 1 }, () => {
      // 
      this.loadBonusPointList();
    });
  };

  render() {
    const {
      dataSource,
      isLoading,
      dateTime,
      type,
      isShow,
      freezeDataSource,
      isFreezeLoading,
      refreshing,
      freezeRefreshing
    } = this.state;
    const height = document.documentElement.clientHeight - 250;

    const {
      bonusInfo: { bonusPoint, bonusFreezePoint, bonusIncome, bonusExpend }
    } = this.props;

    return (
      <div className={classNames(styles['bonus-log'])}>
        <WingBlank>
          <BonusHeader
            bonusTotal={bonusPoint}
            bonusFreeze={bonusFreezePoint}
            onClick={this.handleClick}
          />
        </WingBlank>
        <WhiteSpace />
        <WingBlank>
          <BonusSearch
            income={bonusIncome}
            pay={bonusExpend}
            dateTime={dateTime}
            type={type}
            onCancel={this.handleCancelClick}
            onOk={this.handleOkClick}
          />
        </WingBlank>
        <WhiteSpace />
        <WingBlank>
          <BonusList
            id="bonus-list"
            onChange={this.onEndReached}
            data={dataSource}
            loading={isLoading}
            pageSize={10}
            height={height}
            onRefresh={this.onRefresh}
            refreshing={refreshing}
          />
        </WingBlank>
        {isShow && (
          <BonusModal
            onChange={this.onFreezeEndReached}
            data={freezeDataSource}
            onClick={this.handleClick}
            loading={isFreezeLoading}
            pageSize={10}
            onRefresh={this.onFreezeRefresh}
            refreshing={freezeRefreshing}
          />
        )}
        <DocumentTitle title="" />
        <WechatConfig showShare={false} />
      </div>
    );
  }
}

export default BonusLog;
