import React, { PureComponent } from 'react';
import classNames from 'classnames';
import queryString from 'query-string';

import { userService } from '../../service';
import { checkUserAuth, DocumentTitle, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';

import { RESETPWD_RESULT_CODE } from '../../constants';
import { message, myToast, tools } from '../../utils';
import { appConfig } from '../../config';
import styles from './resetpwd.less';

const phoneReg = /^1\d{10}$/;
const passwordReg = /^[a-zA-Z0-9]{6,18}$/;
const maxNumber = 60;

// 
const basename = appConfig.basename;

/**
 * 
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Resetpwd extends PureComponent {
  constructor(props) {
    super(props);
    this.urlParams = queryString.parse(location.search);
    this.allow = true; // 
    this.isAndroid = tools.isAndroid(); // Android
    if (this.isAndroid) {
      this.password = ''; // 
    }
    this.state = {
      loading: false,
      codeState: false, // 
      captchaVisible: false, // 
      sendMsgText: '', // 
      countDown: 0, // 
      modalKey: '',
      phone: '',
      code: '',
      password: '',
      icode: '' // 
    };

    // 
    this.throttleHandleResetPassword = tools.throttle(this.handleResetPassword, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.countTimer);
  }

  handleInputChange = (key, e) => {
    this.setState({
      [key]: e.target.value
    });
  };

  /**
   * change
   */
  handlePsdChange = (e) => {
    let value = e.target.value;

    if (this.isAndroid) {
      const start = e.target.selectionStart; // input
      if (value.length > this.password.length) {
        // 
        const index = start - 1;
        this.password = this.password.slice(0, index) + value[index] + this.password.slice(index);
        value = value.replace(/./g, '*');
      } else {
        // 
        this.password = this.password.slice(0, start) + this.password.slice(start + 1);
      }
    }

    this.setState({
      password: value
    });
  };

  /**
   * 
   */
  handleResetPassword = () => {
    if (!this.allow) return false;
    const { phone, code } = this.state;
    const password = this.isAndroid ? this.password : this.state.password;
    if (!this.verifyData(phone, password, code)) {
      return false;
    }

    this.allow = false;
    userService
      .resetPassword(phone, password, code)
      .then((data) => {
        this.allow = true;
        myToast.success('', 1500, () => {
          this.resetSucceed();
        });
      })
      .catch((error) => {
        this.allow = true;
        message.error(error.msg || '');
      });
  };

  /**
   * 
   */
  handleSendCode = () => {
    if (!this.allow) return false;
    let { phone, captchaVisible, icode } = this.state;
    if (captchaVisible && !icode) {
      message.info('');
      return false;
    }
    this.setState({ loading: true });

    this.allow = false;
    userService
      .sendResetpwdMsgCode(phone, icode)
      .then((data) => {
        this.allow = true;
        myToast.success('', 1500);
        this.sendMsgCountDown();
      })
      .catch((error) => {
        this.allow = true;
        let { loading } = this.state;
        loading = false;
        // 
        switch (error.code) {
          // 
          case RESETPWD_RESULT_CODE.PHONE_NOT_REGISTED:
            captchaVisible = false;
            message.info(error.msg);
            break;
          // 
          case RESETPWD_RESULT_CODE.INVALID_CODE:
            if (captchaVisible) {
              message.info('');
            } else {
              captchaVisible = true;
            }
            break;
          default:
            message.error(error.msg || '');
            break;
        }
        this.setState(
          {
            loading,
            captchaVisible
          },
          this.handleRefreshCode
        );
      });
  };

  /**
   * 
   */
  sendMsgCountDown = () => {
    this.setState({
      loading: false,
      modalKey: '',
      countDown: maxNumber,
      sendMsgText: '',
      errortip: (
        <small>
          <span>30</span>
        </small>
      )
    });

    // 
    this.countTimer = setInterval(() => {
      const { countDown } = this.state;
      if (countDown === 1) {
        clearInterval(this.countTimer);
      }
      this.setState({
        countDown: countDown - 1
      });
    }, 1000);
  };

  /**
   * 
   */
  resetSucceed = () => {
    const callback = this.urlParams.callback;
    if (callback) {
      window.location.replace(callback);
    } else {
      window.location.replace(`${basename}/login`);
    }
  };

  /**
   * 
   */
  verifyData = (phone, password, code) => {
    if (!phone) {
      message.info('');
      return false;
    }
    if (!phoneReg.test(phone)) {
      message.info('');
      return false;
    }
    if (!code) {
      message.info('');
      return false;
    }
    if (!password) {
      message.info('');
      return false;
    }
    if (!passwordReg.test(password)) {
      message.info('6-18');
      return false;
    }
    return true;
  };

  handleHideModal = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * 
   */
  handleRefreshCode = () => {
    const { captchaVisible } = this.state;
    if (captchaVisible) {
      this.refs.captchaImg.src = '/captcha/get?t=' + Math.random();
    }
  };

  /**
   * render
   */
  renderSendMsgBtn = () => {
    const { phone, countDown } = this.state;
    const sendMsgText = countDown < 1 ? '' : '';
    if (phoneReg.test(phone) && countDown < 1) {
      return (
        <div className={classNames(styles.sendMsgBtn, styles.active)} onClick={this.handleSendCode}>
          {sendMsgText}
        </div>
      );
    }
    return (
      <div className={styles.sendMsgBtn}>{countDown ? countDown + sendMsgText : sendMsgText}</div>
    );
  };

  /**
   * render
   */
  renderCaptchaCode = () => {
    const { icode, captchaVisible } = this.state;
    if (!captchaVisible) return null;
    return (
      <div className={classNames(styles.setItem, styles.icodeWrap, 'clearfix')}>
        <label />
        <input
          type="text"
          maxLength="4"
          value={icode}
          onChange={this.handleInputChange.bind(null, 'icode')}
          placeholder=""
          autoComplete="off"
        />
        <div className={styles['captchaImg']} onClick={this.handleRefreshCode}>
          <img src="" ref="captchaImg" alt="" />
        </div>
      </div>
    );
  };

  render() {
    const { phone, code, password, countDown } = this.state;
    return (
      <div className={styles.regWrapper}>
        <header className={styles.header}>
          <div className={styles.brand}></div>
        </header>
        <div className={styles.content}>
          <div className={styles.section}>
            <div className={classNames(styles.setItem, styles.phoneWarp, 'clearfix')}>
              <label></label>
              <input
                type="tel"
                readOnly={countDown < 1 ? false : true}
                maxLength="11"
                value={phone}
                onChange={this.handleInputChange.bind(null, 'phone')}
                placeholder=""
                autoComplete="off"
              />
            </div>
            {this.renderCaptchaCode()}
            <div className={classNames(styles.setItem, styles.codeWrap, 'clearfix')}>
              <label></label>
              <input
                type="text"
                maxLength="6"
                value={code}
                onChange={this.handleInputChange.bind(null, 'code')}
                placeholder=""
                autoComplete="off"
              />
              {this.renderSendMsgBtn()}
            </div>
            <div className={classNames(styles.setItem, styles.passwordWrap, 'clearfix')}>
              <label></label>
              <input
                type={this.isAndroid ? 'text' : 'password'}
                maxLength="20"
                value={password}
                onChange={this.handlePsdChange}
                placeholder="6-18"
                autoComplete="off"
              />
            </div>
            {/* <p className={styles.errortip}>{errortip}</p> */}
          </div>
          <div className={styles.function}>
            <div className={styles.submitBtn} onClick={this.throttleHandleResetPassword}>
              
            </div>
          </div>
        </div>
        <DocumentTitle title="" />
        <WechatConfig></WechatConfig>
      </div>
    );
  }
}

export default Resetpwd;
