import React, { PureComponent } from 'react';
import queryString from 'query-string';

import { wechatService } from '../../service';
import { AccountsType } from '../../constants';
import { appConfig } from '../../config';
import { message, localStore } from '../../utils';
import { logger } from '../../utils/monitor';

/**
 * code
 * @type {number}
 */
const CODE_ALREADY_USED = 40163;
/**
 * code
 * @type {number}
 */
const CODE_INVALID = 40029;

/**
 * 
 */
export default class WechatAuth extends PureComponent {
  constructor(props) {
    super(props);

    // 
    this.execUserAuth();
  }

  /**
   *  / 
   * @returns {Promise<void>}
   */
  execUserAuth = async () => {
    try {
      // returnUrl url
      const { code, state, ...params } = queryString.parse(window.location.search);
      // 
      const account = params.account;
      //  url
      let returnUrl = params['returnUrl'];

      // : mode   or  , 
      // user: 
      // base: 
      const mode = params['mode'] || 'user';

      let userAuthUrl;
      let openIdKey;

      switch (account) {
        case AccountsType.MAIN:
          userAuthUrl = appConfig.resources.mainUserAuthUrl;
          openIdKey = appConfig.cookie.mainOpenIdKey;
          break;
        case AccountsType.ACTIVITY:
          userAuthUrl = appConfig.resources.activityUserAuthUrl;
          openIdKey = appConfig.cookie.activityOpenIdKey;
          break;
      }

      //   /  url
      const { protocol, host, pathname } = window.location;
      const redirectUri = `${protocol}//${host}${pathname}?${queryString.stringify(params)}`;

      let scope;
      if (mode === 'base') {
        scope = 'snsapi_base'; // 
      } else {
        scope = 'snsapi_userinfo'; // 
      }
      const fullUserAuthUrl = `${userAuthUrl}&redirect_uri=${encodeURIComponent(
        redirectUri
      )}&response_type=code&scope=${scope}&state=1#wechat_redirect`;

      // , 
      if (!code) {
        window.location.replace(fullUserAuthUrl);
        return;
      }

      let res = await wechatService.getOpenIdByCodeAndAccount(code, account);
      if (!res) {
        message.error(', !');
        return;
      }

      if (typeof res === 'string') {
        res = JSON.parse(res);
      }
      console.log('getOpenIdByCodeAndAccount >> res', res);

      // code, 
      if (res.code === CODE_ALREADY_USED || res.code === CODE_INVALID) {
        window.location.replace(fullUserAuthUrl);
        return;
      }

      const openId = res.openId;
      if (!openId) {
        logger.error({
          message: `, openId`
        });
        message.error(', !');
        return;
      }

      localStore.set(openIdKey, openId);

      if (returnUrl) {
        returnUrl = decodeURIComponent(returnUrl);
        // 
        window.location.replace(returnUrl);
      }
    } catch (error) {
      // console.error('WechatAuth', error);
      logger.error({
        message: `: ${JSON.stringify(error)}`
      });
      message.error(', !');
    }
  };

  render() {
    return <div></div>;
  }
}
