// let debugStatus = process.env.NODE_ENV === "production" ? false : true;
const debugStatus = false;

/**
 * 
 * @type {{ready: boolean, error: string}}
 */
export const wechatStatus = {
  /**
   * config
   */
  ready: false,
  /**
   * config
   */
  error: ''
};

/**
 * , config
 * JS-SDK
 *
 * staging: : appid=wxd7ec12bd329457fb
 * mobile : : appid=wxa86689ab270599d4
 * @param wechatSign 
 * @param showShare , 
 * @param shareOptions 
 */
export const wechatConfig = (wechatSign = {}, showShare = true, shareOptions = {}) => {
  const config = {
    debug: debugStatus, // ,apialertpclogpc
    appId: wechatSign.appId, // 
    timestamp: wechatSign.timestamp, // 
    nonceStr: wechatSign.nonceStr, // 
    signature: wechatSign.signature, // 
    jsApiList: [
      'chooseWXPay',
      'chooseImage',
      'onMenuShareTimeline',
      'onMenuShareAppMessage',
      'onMenuShareQQ',
      'onMenuShareWeibo',
      'onMenuShareQZone',
      'hideMenuItems',
      'showMenuItems',
      'hideOptionMenu'
    ] // JS
  };
  wx.config(config);
  wechatReady(showShare, shareOptions);
  wechatError();
};

/**
 * , ready
 * @param showShare 
 * @param options
 */
const wechatReady = (showShare, shareOptions = {}) => {
  wx.ready(function() {
    console.log(' js-sdk wx.ready');
    // configreadyconfig
    // configready
    // ready
    wechatStatus.ready = true;
    wechatStatus.error = '';

    // 
    configShare(showShare, shareOptions);
  });
};

/**
 * 
 * @param showShare
 * @param shareOptions
 */
export const configShare = (showShare, shareOptions = {}) => {
  const location = window.location;
  const defaultLink = `${location.protocol}//${location.host}${location.pathname}`;
  const options = {
    title: appConfig.share.title, // 
    desc: appConfig.share.desc, // 
    link: defaultLink, // 
    imgUrl: appConfig.share.imgUrl, // 
    type: appConfig.share.type, // ,musicvideolinklink
    dataUrl: appConfig.share.dataUrl, // typemusicvideo
    ...shareOptions
  };

  console.log('', ': ' + showShare, options);

  onMenuShareTimeline(options);
  onMenuShareAppMessage(options);
  onMenuShareQQ(options);
  onMenuShareWeibo(options);
  onMenuShareQZone(options);

  //   
  showOrHideMenuShare(showShare);
};

/**
 * , error
 */
const wechatError = () => {
  wx.error(function(res) {
    // configerrorconfigdebugresSPA
    wechatStatus.ready = false;
    wechatStatus.error = JSON.stringify(res);
    console.error('wechatError >> !', res);
  });
};

/**
 * 
 */
export const chooseWXPay = (option) => {
  if (!wechatStatus.ready) {
    console.error('>>>');
    return;
  }
  wx.chooseWXPay({
    timestamp: option.timeStamp, // jssdktimestamptimeStampS
    nonceStr: option.nonceStr, //  32 
    package: option.package, // prepay_idprepay_id=\*\*\*
    signType: option.signType, // 'SHA1''MD5'
    paySign: option.paySign, // 
    success: function(res) {
      message.closeLoading();
      const url = appConfig.course.booking;
      window.location.href = url;
    },
    fail: function(res) {
      message.error('.' + JSON.stringify(res));
    },
    cancel: function(data) {
      message.closeLoading();
    }
  });
};

/**
 * 
 * @param count
 * @param callback 
 */
export const chooseImage = ({ count = 1, callback }) => {
  if (!wechatStatus.ready) {
    console.error('>>>');
    return;
  }
  wx.chooseImage({
    count: count, // 9
    sizeType: ['compressed'], // 
    sourceType: ['album', 'camera'], // 
    success: function(res) {
      callback && callback(res.localIds);
    }
  });
};

/**
 *   
 */
export const showOrHideMenuShare = (showShare) => {
  if (showShare) {
    wx.showMenuItems({
      menuList: [
        'menuItem:share:appMessage',
        'menuItem:share:timeline',
        'menuItem:share:qq',
        'menuItem:share:weiboApp',
        'menuItem:share:facebook',
        'menuItem:share:QZone',
        'menuItem:favorite',
        'menuItem:openWithQQBrowser',
        'menuItem:openWithSafari'
      ]
    });
  } else {
    wx.hideMenuItems({
      menuList: [
        'menuItem:share:appMessage',
        'menuItem:share:timeline',
        'menuItem:share:qq',
        'menuItem:share:weiboApp',
        'menuItem:share:facebook',
        'menuItem:share:QZone',
        'menuItem:favorite',
        'menuItem:openWithQQBrowser',
        'menuItem:openWithSafari'
      ] // menu3
    });
  }
};

