import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import { Tabs } from 'antd-mobile';
import className from 'classnames';
import querystring from 'query-string';
import moment from 'moment';
import _ from 'lodash';

import Footer from '../layout/footer';
import { WechatConfig, DocumentTitle, InfoModal, BaseModal, checkUserAuth } from '../../components';
import { connBasicLayout } from '../layout';
import { ClASS_TYPE, COURSE_STATUS } from '../../constants';
import Banner from './banner';
import ClassList from './classList';

import { userService } from '../../service';

import { localStore, monitor, logger } from '../../utils';
import { appConfig } from '../../config';

import styles from './booking.less';
import styleModal from '../../components/modal/base-modal.less';

// 
const basename = appConfig.basename;
const moreKnowUrl = appConfig.resources.moreKnowUrl;

// 
const surveyPath = `${basename}/survey`;

/**
 * 
 * @type {{title: string}}
 */
const shareOptions = {
  title: '-'
};

// 
const ClassTypeText = { MAIN_1v4: '4', MAIN_1v2: '2' };

// 
const bootstrapKey = appConfig.cookie.bookingBootstrap;

// 
const SURVEY_RESULT = [
  {
    type: 'smallAge',
    text: '',
    link: moreKnowUrl,
    buttonText: '',
    log: () => monitor.log('', '')
  },
  {
    type: 'lessCourse', // 
    buttonText: '',
    text: '',
    link: '',
    log: () => monitor.log('', '')
  },
  {
    type: 'normal',
    buttonText: '',
    text: '',
    link: '',
    log: () => monitor.log('', '')
  }
];

/**
 * 
 */
@inject(({ bookingStore, globalStore }) => {
  return {
    loading: bookingStore.state.applyLoading,
    isShopCar: bookingStore.state.isShopCar,
    isShow: bookingStore.state.isShow,
    modalKey: bookingStore.state.modalKey,
    modalText: bookingStore.state.modalText,
    okText: bookingStore.state.okText,
    isFirstBootstrap: bookingStore.state.isFirstBootstrap,
    bootstrap: bookingStore.state.bootstrap,
    currentSelectLesson: bookingStore.state.currentSelectLesson,
    courseDataObj: bookingStore.state.courseDataObj,
    classType: bookingStore.state.classType,
    subscribeStatus: bookingStore.state.subscribeStatus, // 
    qrcode: bookingStore.state.qrcode, // 
    surplusPackage: bookingStore.state.surplusPackage,
    surplusClassHour: bookingStore.state.surplusClassHour,
    selectClassNum: bookingStore.state.selectClassNum,
    protocol: bookingStore.protocol,

    setStoreState: bookingStore.setStoreState,
    removeSelectLesson: bookingStore.removeSelectLesson,
    submitAppointment: bookingStore.submitAppointment,
    clearCourse: bookingStore.clearCourse,
    showClassList: bookingStore.showClassList,
    getTotal: bookingStore.getTotal,
    handleHideModal: bookingStore.handleHideModal,
    errorModalByGrading: bookingStore.errorModalByGrading,
    getClassUserStatus: bookingStore.getClassUserStatus,
    getClassPackage: bookingStore.getClassPackage,

    confirmProtocol: bookingStore.confirmProtocol, // 

    // globalStore
    courseConfig: toJS(globalStore.state.courseConfig), // 1v2
    getCourseConfig: globalStore.getCourseConfig,
    getProtocolStatus: bookingStore.getProtocolStatus
  };
})
@observer
@connBasicLayout({ useFooter: true, backgdName: 'layoutWallbg' })
@checkUserAuth({ support: 'all', mode: 'base' })
class Booking extends Component {
  constructor(props) {
    super(props);
    this.urlParams = querystring.parse(location.search);
    this.state = {
      showTabs: true,
      classConsume1V2: '1.5', // 1v2
      showGradingModal: false,
      showSuccessModal: false,
      GradingTime: 3,
      level: '', // 
      text: '', // modal
      buttonText: '', // 
      link: '' // 
    };
    // userId
    const userId = localStore.getUserId();
    // survey 
    if (this.urlParams.from === 'register' && !localStore.get(userId)) {
      this.getGradingResult();
      localStore.set(userId, true);
    }
    if (this.urlParams.from === 'landing') {
      this.state.showSuccessModal = true;
      this.gradingTimer();
      localStore.set(userId, true);
    }

    //  / 
    this.props.getClassUserStatus();
    // 
    this.props.getProtocolStatus();
  }

  componentDidMount() {
    monitor.logMoreParam('', '');
    const { classType, getCourseConfig, getClassPackage } = this.props;
    getCourseConfig();
    getClassPackage(classType);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    console.log('Booking >> componentDidUpdate');
  }

  /**
   * , setmodal
   */
  getGradingResult = async () => {
    const { errorModalByGrading } = this.props;
    try {
      const result = await userService.surveyResult();
      const { log = () => {}, ...rest } = SURVEY_RESULT.filter(
        (item) => item.type === result.type
      )[0];
      log();
      this.setState(
        () => ({
          ...rest,
          level: result.level,
          type: result.type,
          showGradingModal: true
        }),
        () => {
          if (result.type === 'lessCourse' || result.type === 'normal') {
            // 
            this.gradingTimer();
          }
        }
      );
      // tools.setTitle('');
      window.scrollTo(0, 0);
    } catch (error) {
      errorModalByGrading('');
      monitor.log('', `:${JSON.stringify(error)}`);
    }
  };

  /**
   * 
   */
  submitAppointment = () => {
    monitor.log('', '');
    const { submitAppointment } = this.props;
    submitAppointment && submitAppointment();
  };

  closeBootstrap = () => {
    localStore.set([bootstrapKey], 'true');
    this.props.setStoreState({
      bootstrap: true,
      isShow: true
    });
  };

  /**
   * @description 
   */
  renderBootstrap = () => {
    const { bootstrap } = this.props;

    if (bootstrap && !localStore.get(bootstrapKey)) {
      return (
        <div
          className={styles.bootstrap}
          style={{ bottom: 'calc(0px + env(safe-area-inset-bottom))' }}
        >
          <div className={styles.iknow} onClick={this.closeBootstrap}>
            
          </div>
        </div>
      );
    }
    return null;
  };

  /**
   * 
   */
  getTotal = () => {
    return this.props.getTotal();
  };

  /**
   * 
   */
  removeSelectLesson = (data) => {
    const { currentSelectLesson } = this.props;
    if (data) {
      monitor.log('', ` =>   => : ${JSON.stringify(data)}`);
      this.props.removeSelectLesson(data);
    } else {
      monitor.log('', ` =>   => : ${JSON.stringify(currentSelectLesson)}`);
      this.clearCourse();
    }
  };

  /**
   *  
   */
  clearCourse = () => {
    const { clearCourse } = this.props;
    clearCourse && clearCourse();
  };

  /**
   * 1v4/1v2
   */
  handleTransfer = (type, idnex) => {
    const { getClassPackage, setStoreState } = this.props;
    setStoreState({
      classType: type.value
    });
    this.clearCourse();
    getClassPackage(type.value);
  };

  /**
   * 
   */
  removeSelectAll = () => {
    this.props.setStoreState({
      modalKey: 'info',
      modalText: '',
      okText: ''
    });
  };

  /**
   * ,href
   * @param path
   * @returns {Function}
   */
  handleHrefChange = (url) => {
    // userId (userId)
    const userId = localStore.getUserId();
    localStore.remove(userId);
    return () => {
      window.location.href = url;
    };
  };

  /**
   * 
   */
  showClassList = () => {
    this.props.showClassList();
  };

  /**
   * modal
   */
  handleHideModal = () => {
    const { modalKey, handleHideModal } = this.props;
    if (modalKey === 'agreement') {
      logger.info({
        message: '',
        eventName: 'client_protocol_close'
      });
    }
    handleHideModal && handleHideModal();
  };

  handleAgree = () => {
    this.props.confirmProtocol();
  };

  renderModal = () => {
    const { modalKey, modalText, loading, okText, courseConfig, protocol } = this.props;

    let element = null;
    switch (modalKey) {
      case 'info':
        element = (
          <InfoModal
            okText={okText}
            headerType="happy"
            loading={loading}
            onOk={this.clearCourse}
            onCancel={this.handleHideModal}
          >
            <div className={styles.modalCentent}>{modalText}</div>
          </InfoModal>
        );
        break;
      case 'error_by_grading':
        element = (
          <InfoModal
            okText={okText}
            headerType="weep"
            cancelVisible={false}
            onOk={this.handleHrefChange(surveyPath)}
          >
            <div className={styles.modalCentent}>{modalText}</div>
          </InfoModal>
        );
        break;
      case 'error':
        element = (
          <InfoModal
            okText={okText}
            headerType="weep"
            cancelVisible={false}
            onOk={this.handleHideModal}
          >
            <div className={styles.modalCentent}>{modalText}</div>
          </InfoModal>
        );
        break;
      case 'no_grading':
        element = (
          <InfoModal
            headerType="weep"
            onCancel={this.handleHideModal}
            onOk={this.handleHrefChange(surveyPath)}
          >
            <div className={styles.modalCentent}></div>
          </InfoModal>
        );
        break;
      case 'notice':
        element = (
          <InfoModal
            headerType="happy"
            cancelVisible={false}
            loading={loading}
            okText=""
            onOk={this.handleHideModal}
          >
            <div className={styles.modalCentent}>
              <ul>
                <li>2</li>
                <li>
                  <span style={{ color: 'red' }}>{courseConfig.consumeCurrency}</span>
                </li>
              </ul>
            </div>
          </InfoModal>
        );
        break;
      case 'agreement':
        // 
        element = (
          <BaseModal
            className={styleModal['backNone']}
            closable={true}
            onCancel={this.handleHideModal}
            loading={loading}
            footer={
              <div className={styles['info-footer']}>
                <button key="ok" className={styles['okBtn']} onClick={this.handleAgree}>
                  
                </button>
              </div>
            }
          >
            <div dangerouslySetInnerHTML={{ __html: protocol.protocolHeader }} />
            <div
              className={styleModal.protocol}
              dangerouslySetInnerHTML={{ __html: protocol.protocolDetail }}
            />
          </BaseModal>
        );

        break;
    }
    return element;
  };

  /**
   * @param status 
   */
  renderCourseStatus = (status) => {
    switch (status) {
      case COURSE_STATUS.FULL_BOOKED:
        return <i></i>;
      case COURSE_STATUS.ALREADY_BOOKED:
      case COURSE_STATUS.ALREADY_BOOKED_MAIN:
        return <i></i>;
      case COURSE_STATUS.ALREADY_BOOKED_1V4:
        return <i>4</i>;
      case COURSE_STATUS.ALREADY_BOOKED_1V2:
        return <i>2</i>;
      case COURSE_STATUS.ALREADY_BOOKED_SPECIAL:
        return <i></i>;
      case COURSE_STATUS.ALREADY_FINISH:
        return <i></i>;
      default:
        return null;
    }
  };

  /**
   *  
   */
  renderClassList = () => {
    const { currentSelectLesson, isShow, classType, courseDataObj } = this.props;
    const keys = Object.keys(currentSelectLesson).sort();
    const weekArr = ['', '', '', '', '', '', ''];
    return (
      <section id="booking-course" className={isShow ? styles.list : ''}>
        <div>
          <h4>
            <span className={styles.selectText}></span>
            <span className={styles.delete} onClick={this.removeSelectAll}>
              
            </span>
          </h4>
          <ul className={styles['course-item']}>
            {keys.map((item) => {
              const tempArr = [];

              // 
              currentSelectLesson[item].forEach((subLesson) => {
                tempArr.push(JSON.parse(subLesson));
              });
              // 
              //      
              //    
              const courseAll = [];
              const courseNormal = [];
              Object.keys(courseDataObj).forEach((key) => {
                courseDataObj[key].length > 0 &&
                  courseDataObj[key].forEach((item) => {
                    if (item.courseStatus !== COURSE_STATUS.NORMAL) {
                      courseAll.push(item.startTime);
                    }
                    if (item.courseStatus === COURSE_STATUS.NORMAL) {
                      courseNormal.push(item.startTime);
                    }
                  });
              });
              const fullBooked = courseAll.filter(
                (item) => item.courseStatus !== COURSE_STATUS.NORMAL
              );
              // 
              tempArr.sort((prev, nex) => prev.id - nex.id);

              return (
                <li key={item}>
                  {tempArr.map((val, key) => {
                    return (
                      <p key={`${item}${val.id}`}>
                        <span className={styles.left}>
                          {moment(item).format('MM/DD')}&nbsp;&nbsp;
                          {weekArr[new Date(item).getDay()]}({ClassTypeText[classType]})
                          {fullBooked.indexOf(val.startTime) > -1
                            ? this.renderCourseStatus(COURSE_STATUS.FULL_BOOKED)
                            : courseNormal.indexOf(val.startTime) > -1
                            ? this.renderCourseStatus(COURSE_STATUS.NORMAL)
                            : this.renderCourseStatus(val.tempCourseStatus)}
                        </span>
                        <span className={styles.right}>
                          {val.beginTime}-{val.endTime}
                          <em onClick={this.removeSelectLesson.bind(null, { item, id: val.id })} />
                        </span>
                      </p>
                    );
                  })}
                </li>
              );
            })}
          </ul>
        </div>
      </section>
    );
  };

  renderTabs = () => {
    const { classType, courseConfig } = this.props;
    if (_.isEmpty(courseConfig)) {
      return null;
    }
    if (courseConfig.bookFlag) {
      return (
        <Tabs
          tabs={[
            { title: '4', value: 'MAIN_1v4' },
            { title: '2', value: 'MAIN_1v2' }
          ]}
          useOnPan={false}
          swipeable={false}
          // animated={false}
          activeTab={classType}
          tabBarUnderlineStyle={{ display: 'none' }}
          onChange={this.handleTransfer}
        >
          <ClassList classType={ClASS_TYPE.MAIN_1v4} activeKey={classType} />
          <ClassList classType={ClASS_TYPE.MAIN_1v2} activeKey={classType} />
        </Tabs>
      );
    } else {
      return (
        <ClassList
          classType={ClASS_TYPE.MAIN_1v4}
          activeKey={classType}
          style={{ borderRadius: '10px' }}
        />
      );
    }
  };

  /**
   *  3smodal
   */
  gradingTimer = () => {
    const { GradingTime } = this.state;
    let ti = GradingTime;

    const timeChange = setInterval(() => {
      if (ti > 1) {
        ti = ti - 1;
        this.setState({
          GradingTime: ti
        });
      } else {
        clearInterval(timeChange);
        this.setState({
          GradingTime: 3,
          showGradingModal: false,
          showSuccessModal: false
        });
      }
    }, 1000);
  };

  /**
   * modal
   */
  closeGradingModal = () => {
    const link = this.state.link;
    // link  or 
    if (link === '') {
      monitor.logMoreParam('', '');
    } else {
      setTimeout(() => {
        location.href = link;
      }, 300);
    }
    this.setState({
      showGradingModal: false,
      showSuccessModal: false
    });
  };

  /**
   * modal
   */
  renderGradingModal = () => {
    const { showGradingModal, GradingTime, level, text, buttonText, type } = this.state;
    return showGradingModal ? (
      <div className={styles.gradingModal}>
        <BaseModal
          header={<div className={styles.gradingHeader}>LV{level}</div>}
          footer={
            <div className={styles.gradingBtn} onClick={this.closeGradingModal}>
              {buttonText}
              {type === 'lessCourse' || type === 'normal' ? `(${GradingTime}s)` : null}
            </div>
          }
        >
          <p className={styles.gradingFont}>{text}</p>
        </BaseModal>
      </div>
    ) : null;
  };

  /**
   * modal
   */
  renderSuccessModal = () => {
    const { showSuccessModal, GradingTime } = this.state;
    return showSuccessModal ? (
      <div className={styles.gradingModal}>
        <BaseModal
          header={<div className={className(styles.gradingHeader, styles.yeah)} />}
          footer={
            <div className={styles.gradingBtn} onClick={this.closeGradingModal}>
              {``}
              {`(${GradingTime}s)`}
            </div>
          }
        >
          <p className={styles.gradingFont}></p>
          <p className={styles.gradingFont}></p>
        </BaseModal>
      </div>
    ) : null;
  };

  /**
   * 
   */
  renderNextPackage = () => {
    const {
      classType,
      surplusPackage,
      surplusClassHour,
      selectClassNum,
      courseConfig: { consumeCurrency }
    } = this.props;
    // 
    if (!surplusPackage) {
      return null;
    }
    switch (classType) {
      case 'MAIN_1v4':
        if (selectClassNum <= surplusClassHour) {
          return null;
        }
        break;
      case 'MAIN_1v2':
        if (selectClassNum * consumeCurrency <= surplusClassHour) {
          return null;
        }
        break;
    }
    return <div className={styles.bottomPrompt}> * {surplusPackage}~</div>;
  };

  render() {
    const { isShopCar, isShow, isFirstBootstrap, classType, modalKey } = this.props;
    if (modalKey === 'agreement') {
      document.body.style.cssText += 'position:fixed;width:100%;top:0px;';
    } else {
      document.body.style.position = '';
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      document.body.style.top = '';
    }
    return (
      <div className={styles.wrapper}>
        {this.renderGradingModal()}
        {this.renderSuccessModal()}
        {this.renderBootstrap()}
        <DocumentTitle title="" />
        <header className={styles.header}>
          <Banner />
        </header>
        {this.renderTabs()}
        {this.renderClassList()}
        <footer
          id="booking-footer"
          className={className(styles.shoppingCar, { [styles.footer]: isShopCar })}
        >
          <div className={styles.footerWrapper}>
            <p onClick={this.showClassList}>
              <span className={styles.images} />
              <span className={styles['course-select']}></span>
              <span className={styles.number}>
                {this.getTotal()}({ClassTypeText[classType]})
              </span>
            </p>
            <div className={styles.submitButtonWrapper}>
              <div
                className={className(styles.submitButton, {
                  [styles.tada]: isFirstBootstrap && isShow
                })}
                onClick={this.submitAppointment}
              >
                
              </div>
            </div>
          </div>
          {this.renderNextPackage()}
        </footer>
        {isShow && <p className={styles.mark} onClick={this.showClassList} />}
        {this.renderModal()}
        <Footer />
        <WechatConfig showShare={true} shareOptions={shareOptions} />
      </div>
    );
  }
}

export default Booking;
