import React from 'react';
import { Carousel } from 'antd-mobile';
import { observer, inject } from 'mobx-react';
import WX from 'weixin-js-sdk';

import { checkUserAuth, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';
import { resourceService } from '../../service';
import { appConfig } from '../../config';

import { wechatUtils, tools, message } from '../../utils';
import { logger } from '../../utils/monitor';

import styles from './index.less';

/**
 * 
 * @type {{title: string}}
 */
const shareOptions = {
  title: '-',
  desc: ', ~'
};

/**
 * @description 
 */
@inject(({ globalStore }) => ({
  set_promotion_file: globalStore.set_promotion_file
}))
@observer
@connBasicLayout()
@checkUserAuth({ mode: 'base' })
class Promotion extends React.Component {
  state = {
    current: 0,
    list: [], // 
    loading: false // loading
  };

  componentDidMount() {
    tools.setTitle('');
    logger.info({
      message: ``
    });
    this.props.set_promotion_file();

    // 
    this.loadPosterData();
  }

  /**
   * 
   */
  loadPosterData = async () => {
    // loading
    this.setState({ loading: true });

    try {
      // banner
      const list = await resourceService.getDemoPosterList();

      // 
      this.setState({ list });
    } catch (error) {
      logger.error({
        message: `, ${JSON.stringify(error)}`
      });
      console.error(error);
    }

    // loading
    this.setState({ loading: false });
  };

  /**
   * 
   *    OSS(mmears-prod.oss-cn-beijing.aliyuncs.com)  
   * @returns {*|void}
   */
  handleUpload = () => {
    const context = this;
    logger.info({
      message: ``
    });
    if (!tools.isWeiXin()) {
      return message.info('');
    }

    wechatUtils.chooseImage({
      callback(files) {
        WX.getLocalImgData({
          localId: files[0],
          async success(res) {
            let localData = res.localData;

            if (window.__wxjs_is_wkwebview) {
              // IOS
              localData = localData.replace('jgp', 'jpeg');
            } else {
              // 
              localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '');
            }

            //    OSS(mmears-prod.oss-cn-beijing.aliyuncs.com)  
            // localData = 'https://mmears-prod.oss-cn-beijing.aliyuncs.com/mmears-weixin/sharePoster-1022780.png';
            // 
            try {
              const { url } = await resourceService.choiceImageUpload(localData);
              // 
              if (url) {
                context.props.set_promotion_file(url);
                context.props.history.push(`/promotion_detail?current=${context.state.current}`);
              }
            } catch (error) {
              if (error && error.code === 'LOGIN_REQUIRED') {
                window.location.replace(
                  `${appConfig.basename}/login?callback=${encodeURIComponent(window.location.href)}`
                );
                return;
              }

              logger.error({
                message: `, ${JSON.stringify(error)}`
              });

              message.error((error && error.msg) || '');
            }
          }
        });
      }
    });
  };

  /**
   * @description   chrome   
   * @param from
   * @param to
   * @return {boolean}
   */
  handleChange = (from, to) => {
    this.setState({
      current: to
    });
    return false;
  };

  /**
   *  
   */
  renderPoster = () => {
    const { list } = this.state;

    return list.map((imgUrl) => {
      return (
        <div
          className={styles.posterItem}
          key={imgUrl}
          onClick={(event) => {
            event.preventDefault();
          }}
        >
          <div className={styles.mask} />
          <img src={imgUrl} alt="" />
        </div>
      );
    });
  };

  /**
   *  Banner
   */
  renderCarousel = () => {
    return (
      <Carousel autoplay={false} infinite beforeChange={this.handleChange}>
        {this.renderPoster()}
      </Carousel>
    );
  };

  /**
   *  loading
   */
  renderLoading = () => {
    return (
      <div className={styles['containers']}>
        <div className={styles['m-load2']}>
          <div className={styles['line']}>
            <div />
            <div />
            <div />
            <div />
            <div />
            <div />
          </div>
          <div className={styles['circlebg']} />
        </div>
      </div>
    );
  };

  render() {
    // 
    const { loading } = this.state;
    return (
      <div className={styles.posterWrapper}>
        <div className={styles.posterBg}>
          {loading ? this.renderLoading() : this.renderCarousel()}
        </div>
        <div className={styles.makeWrppaer}>
          <button className={styles.makeBtn} onClick={this.handleUpload}>
            
          </button>
        </div>
        <WechatConfig showShare={true} shareOptions={shareOptions} />
      </div>
    );
  }
}

export default Promotion;
