import React from 'react';
import {Picker, List, DatePicker} from 'antd-mobile';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import moment from 'moment';

import {BONUS_TRADE_TYPE, BONUS_DATE_RANGE} from '../../constants';

import styles from './bonusLog.less';


// 
const BonusSearch = (props) => {
    const {income, pay, dateTime, type, onCancel, onOk} = props;

    return (
        <div className={classNames(styles['bonus-log-search'])}>
            <div className={classNames(styles['bonus-search-time'])}>
                <DatePicker
                    mode={'month'}
                    extra=""
                    value={dateTime}
                    onDismiss={() => onCancel(null, 'dateTime')}
                    format={date => moment(date).format('YYYY-MM')}
                    onOk={(e) => onOk(e, 'dateTime')}
                    minDate={BONUS_DATE_RANGE.MIN_DATE}
                    maxDate={BONUS_DATE_RANGE.MAX_DATE}
                >
                    <List.Item arrow="horizontal">
                    </List.Item>
                </DatePicker>
            </div>
            <div className={classNames(styles['bonus-search-type'])}>
                <Picker
                    data={BONUS_TRADE_TYPE}
                    cols={1}
                    extra=""
                    value={type}
                    onDismiss={() => onCancel(['ALL'], 'type')}
                    onOk={(e) => onOk(e, 'type')}
                >
                    <List.Item arrow="horizontal">
                    </List.Item>
                </Picker>
            </div>
            <div className={classNames(styles['bonus-search-desc'])}>
                <span className={classNames(styles['bonus-desc-left'])}>: {income}</span>
                <span className={classNames(styles['bonus-desc-right'])}>: {pay}</span>
            </div>
        </div>
    );
};

// 
BonusSearch.propTypes = {
    income: PropTypes.number, // 
    pay: PropTypes.number, // 
    dateTime: PropTypes.object, // 
    type: PropTypes.any, // 
    onCancel: PropTypes.func.isRequired, // 
    onOk: PropTypes.func.isRequired, // 
};

// 
BonusSearch.defaultProps = {
    income: 0, // 0
    pay: 0, // 0
    dateTime: null,
    type: [],
};

export default BonusSearch;
