import React from 'react';

import { checkUserAuth, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';

import styles from './index.less';
import * as tools from '../../utils/tools';

import { userService } from '../../service';
import monitor from '../../utils/monitor';
import { appConfig } from '../../config';

// 
const basename = appConfig.basename;
const moreKnowUrl = appConfig.resources.moreKnowUrl;
const faqUrl = appConfig.resources.faqUrl;
// 
const SURVEY_RESULT = [
  {
    type: 'smallAge',
    text: '',
    link: moreKnowUrl,
    buttonText: '',
    log: () => monitor.log('', '')
  },
  {
    type: 'lessCourse', // 
    buttonText: '',
    text: '10~',
    link: `${basename}/booking?${tools.getSearchParam(window.location.href)}`,
    log: () => monitor.log('', '')
  },
  {
    type: 'missDevice',
    text: '~',
    link: faqUrl,
    buttonText: '',
    log: () => monitor.log('', '')
  },
  {
    type: 'normal',
    buttonText: '',
    text: '10~',
    link: `${basename}/booking?${tools.getSearchParam(window.location.href)}`,
    log: () => monitor.log('', '')
  }
];

/**
 * 
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
export default class SurveyResult extends React.Component {
  constructor(props) {
    super(props);
    this.inWebView = tools.checkInWebView();
    this.state = {
      buttonText: '',
      text: '10~',
      link: '',
      level: 1
    };
  }

  async componentDidMount() {
    const result = await userService.surveyResult();
    const { log = () => {}, ...rest } = SURVEY_RESULT.filter(
      (item) => item.type === result.type
    )[0];
    log();
    this.setState(() => ({
      ...rest,
      level: result.level
    }));
    tools.setTitle('');
    window.scrollTo(0, 0);
  }

  handleClick = () => {
    monitor.logMoreParam('', '');
    // if (this.state.link.indexOf('http') > -1) {
    let link = this.state.link;
    if (this.inWebView) {
      link = link.replace(/\/booking/, '/minibooking');
    }
    setTimeout(() => {
      location.href = link;
    }, 300);
    // } else {
    //     const {act = ''} = querystring.parse(location.search);
    //     window.location.replace(`${this.state.link}?${act ? `act=${act}` : ''}`);
    // }
  };

  render() {
    const { level, buttonText, text } = this.state;
    return (
      <div className={styles['survey-result']}>
        <div className={styles.card}>
          <div className={styles.title}></div>
          <div className={styles.lv}>
            <img src={`static/img/lv${level}.png`} alt="" />
          </div>
          <div className={styles['dot-title']}></div>
          <p className={styles.desc}></p>
          <div className={styles['dot-title']}></div>
          <p className={styles.desc}>{text}</p>
          <p className={styles.desc}>-</p>
        </div>
        <div className={styles.footer}>
          <button className={styles.submitBtn} onClick={this.handleClick}>
            {buttonText}
          </button>
        </div>
        <WechatConfig></WechatConfig>
      </div>
    );
  }
}
