import React, { Component } from 'react';

import { connBasicLayout } from '../layout';

import { checkUserAuth, DocumentTitle } from '../../components';

import styles from './special.less';

import specialDetaillStyles from './special-detail.less';

import weepRabbit from '../../public/images/special/cry_rabbit.png';

import { specialService } from '../../service';
import { message } from '../../utils';
import { appConfig } from '../../config';

import * as specialTools from './tools';

import { BirthdayModal, SpecialModal, WeiXinModal } from './component';

import { MODAL_KEY, BUTTON_TEXT } from '../../constants/special';

const basename = appConfig.basename;

/**
 * @description 
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Special extends Component {
  state = {
    specialData: [], // 
    modalKey: '',
    buttonText: BUTTON_TEXT.BUYING_BTN, // 
    buttonUrl: '', // 
    currentspId: '', // id
    recommendAgeLevel: '',
    judgeText: '',
    recommend_spid: '',
    loading: false
  };

  componentDidMount() {
    this.loadData();
  }

  /**
   * @description 
   * @return element
   */
  renderModal = () => {
    const { modalKey, recommendAgeLevel, judgeText } = this.state;
    //  
    switch (modalKey) {
      case MODAL_KEY.NO_MATCH_CLASS: {
        return (
          <WeiXinModal
            header={
              <div>
                <p>
                  400-626-162610:00-22:00
                </p>
              </div>
            }
            onCancel={this.clearModalKey}
          />
        );
      }
      case MODAL_KEY.BIRTHDAY: {
        return (
          <BirthdayModal
            onOk={specialTools.handleUpdateBirthDay.bind(this)}
            onCancel={this.clearModalKey}
          />
        );
      }
      case MODAL_KEY.SPECIAL_CLASS_END: {
        return (
          <WeiXinModal
            header={
              <div>
                <p>~</p>
              </div>
            }
            onCancel={this.clearModalKey}
          />
        );
      }
      case MODAL_KEY.RECOMMEND_CLASS: {
        return (
          <SpecialModal showCancelButton={true} onCancel={this.clearModalKey}>
            <div className={specialDetaillStyles.recommendClass}>
              <p>
                {judgeText}<br /> {recommendAgeLevel}
                
              </p>
              <div
                className={specialDetaillStyles.btn}
                onClick={specialTools.handleJumpToSpecialDetail.bind(
                  this,
                  this.state.recommend_spid
                )}
              >
                
              </div>
            </div>
          </SpecialModal>
        );
      }
      default: {
        return null;
      }
    }
  };

  /**
   * @description modal_key
   */
  clearModalKey = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * @description 
   */
  loadData = () => {
    this.setState({
      loading: true
    });
    Promise.all([specialService.getSpecialList(), specialService.getSepcialButtonStatus()])
      .then((res) => {
        message.closeLoading();
        this.setState({
          loading: false,
          specialData: res[0] || [],
          buttonText: BUTTON_TEXT[res[1].button],
          buttonUrl: res[1].url
        });
      })
      .catch(() => {
        message.error('');
        this.setState({
          loading: false
        });
      });
  };

  /**
   * @description 
   * @param id id
   */
  handleJump = (id) => {
    window.location.href = basename + `/special_detail/${id}`;
  };

  /**
   * @description  
   */
  renderSpecialView = () => {
    const { specialData = [], buttonText, loading } = this.state;
    if (loading) return null;

    if (specialData.length > 0) {
      return (
        <div className={styles.content_list}>
          {specialData.map((item) => {
            return (
              <div className={styles.list_item} key={item.spId}>
                <div className={styles.list_item_image}>
                  <img
                    src={item.coverImageUrl}
                    alt=""
                    onClick={this.handleJump.bind(null, item.spId)}
                  />
                </div>
                <div className={styles.list_item_info}>
                  <h3>{item.zhName}</h3>
                  <h4>{item.enName}</h4>
                  <div className={styles.list_item_bot}>
                    <span>{item.ageLevel}</span>
                    <button onClick={specialTools.handleJumpTo.bind(this, item.spId)}>
                      {buttonText}
                    </button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      );
    } else {
      //  
      return (
        <div className={styles.empty}>
          <img src={weepRabbit} alt="" />
          <p>
            
            <br />
            
          </p>
        </div>
      );
    }
  };

  render() {
    return (
      <div className={styles.container}>
        <DocumentTitle title="-" />
        {this.renderSpecialView()}
        {this.renderModal()}
      </div>
    );
  }
}

export default Special;
