import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { configure } from 'mobx';
import { Provider } from 'mobx-react';
import Monitor from '@mmears/monitor';

import router from './router';
import * as allStore from './store';
import { appConfig } from './config';

import * as tools from '@mmears/mmears-tools';

// 
const monitor = new Monitor();

/**
 * Mobx
 */
configure({
  enforceActions: 'observed'
});

class App extends Component {
  componentDidMount() {
    // 
    monitor.init();

    // 
    if ((appConfig.env === 'staging' || appConfig.env === 'preview') && tools.isMobile()) {
      const script = document.createElement('script');
      script.src = '//cdn.bootcss.com/eruda/1.5.8/eruda.min.js';
      document.body.appendChild(script);
      script.onload = function() {
        window.eruda && window.eruda.init();
      };
    }
  }

  componentWillUnmount() {
    // 
    monitor.destroy();
  }

  render() {
    //  
    const basename = appConfig.basename;

    return (
      <Router basename={basename}>
        <Provider {...allStore}>{router}</Provider>
      </Router>
    );
  }
}

export default App;
