import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import BonusList from './bonusList';

import styles from './bonusLog.less';

const height = (document.documentElement.clientHeight * 2) / 5;

// 
const BonusModal = (props) => {
  const { onClick, data, pageSize, onChange, loading, onRefresh, refreshing } = props;

  /**
   * 
   */
  const renderList = () => {
    return (
      <BonusList
        onChange={onChange}
        data={data}
        loading={loading}
        pageSize={pageSize}
        height={height}
        freeze={true}
        refreshing={refreshing}
        onRefresh={onRefresh}
      />
    );
  };

  return (
    <div className={classNames(styles['bonus-modal'])}>
      <div className={classNames(styles['bonus-mask'])} />
      <div className={classNames(styles['bonus-card'])}>
        <div className={classNames(styles['bonus-body-bg'])} />
        <div className={classNames(styles['bonus-body'])}>
          <div className={classNames(styles['title'])} />
          <div className={classNames(styles['content'])}>
            <span className={classNames(styles['title'])}>
              130~
              <br />
              21~
              <br />
              360~
            </span>
            <div className={classNames(styles['list'])} style={{ height: height + 20 }}>
              {renderList()}
            </div>
          </div>
        </div>
        <div className={classNames(styles['close'])} onClick={onClick} />
      </div>
    </div>
  );
};

// 
BonusModal.propTypes = {
  refreshing: PropTypes.bool, // 
  loading: PropTypes.bool, // loading
  pageSize: PropTypes.number, // 
  data: PropTypes.any, // 
  onClick: PropTypes.func.isRequired, // 
  onChange: PropTypes.func.isRequired, // 
  onRefresh: PropTypes.func.isRequired // 
};

// 
BonusModal.defaultProps = {
  refreshing: false, // false
  loading: false, // 
  pageSize: 10, // 10
  data: [] // 
};

export default BonusModal;
