import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import Slider from 'react-slick';
import className from 'classnames';
import moment from 'moment';
import _ from 'lodash';
import querystring from 'query-string';
import PropTypes from 'prop-types';

import { COURSE_STATUS, SURPLUS_COURSE, HAPPY_NEW_YEAR_DATE } from '../../constants';

import { bookingService } from '../../service';
import { message, monitor } from '../../utils';

import styles from './classList.less';

/**
 * slider
 */
const DateSettings = {
  // dots: false,
  infinite: false, // 
  arrows: true, // 
  // autoplay: true, // 
  // autoplaySpeed: 2000,
  speed: 500,
  slidesToShow: 7, // 
  slidesToScroll: 7 // 
};

// 
const ClassType = {
  MAIN_1v4: 'MAIN_1v4',
  MAIN_1v2: 'MAIN_1v2'
};

/**
 * 
 */
@inject(({ bookingStore }) => {
  return {
    setLocalData: bookingStore.state.setLocalData,
    courseDataObj: bookingStore.state.courseDataObj,
    currentSelectLesson: toJS(bookingStore.state.currentSelectLesson),
    bootstrap: bookingStore.state.bootstrap,
    updateCourseList: bookingStore.state.updateCourseList,
    protocol: bookingStore.protocol,

    handleSetModalKey: bookingStore.handleSetModalKey,
    handleHideModal: bookingStore.handleHideModal,
    setStoreState: bookingStore.setStoreState,
    changeClassNum: bookingStore.changeClassNum
  };
})
@observer
class ClassList extends Component {
  static propTypes = {
    classType: PropTypes.string,
    activeKey: PropTypes.string,
    style: PropTypes.object
  };

  static defaultProps = {
    style: {}
  };

  constructor(props) {
    super(props);

    this.urlParams = querystring.parse(location.search);

    this.state = {
      loading: false,
      refresh: false, // 
      daysData: [], // 
      courseData: null, // 
      nowDate: moment().format('YYYY-MM-DD'), // 
      currentSelect: moment().format('YYYY-MM-DD'), // 
      happyNewYear: false, // 
      classType: this.props.classType, // 
      selectedActive: false // 
    };
  }

  componentDidMount() {
    const { classType, activeKey } = this.props;
    if (classType === activeKey) {
      this.loadData();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.classType === nextProps.activeKey) {
      // tab or  `updateCourseList` 
      if (
        this.props.activeKey !== nextProps.activeKey ||
        nextProps.updateCourseList !== this.props.updateCourseList
      ) {
        if (!this.state.daysData.length) {
          this.loadData();
        } else {
          // 
          this.getCourseList();
        }
      } else if (!_.isEqual(this.props.currentSelectLesson, nextProps.currentSelectLesson)) {
        this.settingCourseSelect(nextProps.currentSelectLesson);
      }

      // ,  .  ,  courseDataObj 
      if (this.props.setLocalData !== nextProps.setLocalData) {
        this.setState({
          courseData: this.props.courseDataObj
        });
      }
    }
  }

  /**
   * 
   */
  loadData = async () => {
    // if (!this.props.protocol.status) {
    //     this.props.handleSetModalKey('agreement');
    // }
    try {
      message.showLoading();
      // 
      const daysData = await bookingService.getdayList();
      // 
      const happyNewYear = this.isHappyNewYear(daysData.now);

      // 
      const courseData = this.getCourseList(daysData.now, this.state.classType);
      message.closeLoading();
      this.setState({
        daysData: daysData.days,
        nowDate: daysData.now,
        currentSelect: daysData.now,
        courseData,
        happyNewYear
      });

      // 
      this.catchData = courseData;
      const slideIndex = daysData.days.findIndex((t) => t === daysData.now);
      this.dateSlider.slickGoTo(slideIndex > 6 ? 7 : 0);
    } catch (error) {
      message.closeLoading();
      monitor.log(
        '',
        `minibooking/${error && error.msg}`,
        `${error && error.code}`
      );
    }
  };

  /**
   * 
   */
  handleErrorCode = (error) => {
    this.setState({
      refresh: true,
      selectedActive: false
    });
    monitor.log('', `/${error && error.msg}`, `${error && error.code}`);
  };

  /**
   * 
   */
  handleSelect = (date) => {
    // 
    const happyNewYear = this.isHappyNewYear(date);
    monitor.log('', ` =>   => : ${JSON.stringify(date)}`);
    const { currentSelect, selectedActive } = this.state;
    if (date !== currentSelect && !selectedActive) {
      this.setState({
        currentSelect: date,
        courseData: [],
        selectedActive: true,
        happyNewYear
      });
      this.getCourseList(date);
    }
  };

  /**
   * 
   */
  getCourseList = (date, classType) => {
    if (!this.props.protocol.status) {
      this.props.handleSetModalKey('agreement');
    }
    // 
    bookingService
      .getCourseList(date || this.state.currentSelect, classType || this.state.classType)
      .then((courseData) => {
        // 
        this.catchData = courseData;
        this.setState({
          courseData,
          refresh: false,
          selectedActive: false
        });
        // 
        this.props.setStoreState({
          courseDataObj: courseData
        });
      })
      .then(() => {
        this.settingCourseSelect();
      })
      .catch((error) => {
        this.handleErrorCode(error);
      });
  };

  /**
   * @description 
   * @param 
   *
   */
  isHappyNewYear = (currentDate) => {
    // 
    return !!HAPPY_NEW_YEAR_DATE.find((date) => date === currentDate);
  };

  /**
   * animation 
   * cb        
   */
  getAnimation = (ele, cb) => {
    const { left, top, width, height } = ele.getClientRects()[0];
    const emelement = document.createElement('em');
    emelement.setAttribute('class', styles.embox);

    let footerEle = null;
    let targetTop = 0;
    const targetLeft = '2.84rem';
    const fontSize = document.documentElement.style.fontSize.split('p')[0];

    footerEle = document.getElementById('footer').getClientRects()[0];
    targetTop = (footerEle.top + 116) / fontSize + 'rem';

    const group = emelement;
    group.style.top = top + height / 2 + 'px';
    group.style.left = left + width / 2 + 'px';
    document.body.appendChild(group);
    setTimeout(() => {
      group.style.top = targetTop;
      group.style.left = targetLeft;
      group.style.opacity = 0;
      cb && cb();
      setTimeout(() => {
        document.body.removeChild(group);
      }, 600);
    }, 0);
  };

  /**
   * 
   */
  settingCourseSelect = (selectLesson) => {
    const { currentSelect, courseData } = this.state;
    const currentSelectLesson = selectLesson || this.props.currentSelectLesson;
    if (currentSelectLesson[currentSelect]) {
      const data = {};
      const ids = currentSelectLesson[currentSelect].map((item) => {
        return JSON.parse(item).id;
      });
      for (const key in courseData) {
        if (courseData.hasOwnProperty(key) && courseData[key] && courseData[key].length > 0) {
          const tempData = courseData[key].map((subItem) => {
            let temp = { ...subItem };
            if (subItem.courseStatus === 'NORMAL' && ids.indexOf(subItem.id) === -1) {
              temp = {
                ...subItem,
                courseStatus: 'FULL_BOOKED'
              };
            }
            return temp;
          });
          data[key] = tempData;
        }
      }
      this.setState({
        courseData:
          currentSelectLesson[currentSelect].length >= this.surplusCourse ? data : this.catchData
      });
    } else {
      this.setState({
        courseData: this.catchData
      });
    }
  };

  /**
   * 
   */
  makeAppointment = (date, e) => {
    monitor.logMoreParam('', '');
    //  currentSelectLesson
    const { currentSelect } = this.state;
    const { currentSelectLesson, changeClassNum } = this.props;
    const selectLesson = _.cloneDeep(toJS(currentSelectLesson));
    let isShopCar = false;
    const isHave = _.toPairs(currentSelectLesson).some((items) => {
      let truly = false;
      if (items[0] === currentSelect) {
        items[1].forEach((key) => {
          if (_.isEqual(JSON.parse(key)['id'], date['id'])) {
            truly = true;
          }
        });
      }
      return truly;
    });
    if (isHave) {
      // 
      _.toPairs(currentSelectLesson).forEach((item) => {
        if (item[0] === currentSelect) {
          selectLesson[item[0]] = item[1].filter(
            (key) => !_.isEqual(JSON.parse(key)['id'], date['id'])
          );
        }
      });

      _.toPairs(selectLesson).forEach((item) => {
        if (item[1].length > 0) {
          isShopCar = true;
        }
      });
    } else {
      isShopCar = true;
      // 
      this.getAnimation(e.currentTarget);
      if (selectLesson[currentSelect]) {
        const index = selectLesson[currentSelect].indexOf(JSON.stringify(date));
        if (index > -1) {
          selectLesson[currentSelect].splice(index, 1);
        } else {
          selectLesson[currentSelect].push(JSON.stringify(date));
        }
      } else {
        selectLesson[currentSelect] = [JSON.stringify(date)];
      }
    }
    let { bootstrap } = this.props;
    if (!bootstrap) {
      bootstrap = true;
    }
    this.props.setStoreState({
      isShopCar: isShopCar,
      currentSelectLesson: selectLesson,
      bootstrap: bootstrap
    });
    let selectClass = [];
    for (const key in selectLesson) {
      if (selectLesson[key].length) {
        selectClass = selectClass.concat(selectLesson[key]);
      }
    }
    changeClassNum(selectClass);
  };

  /**
   * ,href
   * @param path
   * @returns {Function}
   */
  handleHrefChange = (url) => {
    return () => {
      window.location.href = url;
    };
  };

  /**
   * 
   */
  renderDateView = () => {
    const { daysData, currentSelect, nowDate, classType } = this.state;
    if (!daysData) return false;
    const dataList = [];
    daysData.forEach((item, index) => {
      let classname = '';
      if (item === nowDate) {
        // 
        classname = styles.nowActive;
      }
      if (item === currentSelect) {
        classname = styles.active; // 
      }
      dataList.push(
        <li
          key={classType + item}
          className={classname}
          onClick={this.handleSelect.bind(null, item)}
        >
          <p>{this.getCurrentWeek(item)}</p>
          <p>{moment(item).format('MM/DD')}</p>
        </li>
      );
    });
    return dataList;
  };

  /**
   * 
   */
  renderCourseView = () => {
    const { courseData } = this.state;
    if (!courseData) return false;
    // 
    this.surplusCourse = SURPLUS_COURSE;

    const courseObj = [courseData.am, courseData.pm, courseData.en];
    const timeText = ['', '', ''];
    const courseList = [];

    courseObj.forEach((list, index) => {
      if (list && list.length > 0) {
        const tempHtml = (
          <div key={index} className={className(styles.section, 'clearfix')}>
            <div className={styles.left}>{timeText[index]}</div>
            <div className={styles.right}>
              <ul>{this.renderCourseItem(list)}</ul>
            </div>
          </div>
        );
        courseList.push(tempHtml);
      }
    });
    //
    // let courseList = [];
    // for (let key in courseData) {
    //     if (courseData.hasOwnProperty(key) && courseData[key] && courseData[key].length > 0) {
    //         let tempHtml = (
    //             <div key={key} className={className(styles.section, 'clearfix')}>
    //                 <div className={styles.left}>{TimeQuantum[key]}</div>
    //                 <div className={styles.right}>
    //                     <ul>
    //                         {this.renderCourseItem(courseData[key])}
    //                     </ul>
    //                 </div>
    //             </div>);
    //         courseList.push(tempHtml);
    //     }
    // }
    return courseList;
  };

  /**
   * render
   */
  renderCourseItem = (items) => {
    const { currentSelect, happyNewYear } = this.state;
    const { currentSelectLesson } = this.props;
    const currentSelectList = currentSelectLesson[currentSelect] || [];
    return items.map((item) => {
      // let index = currentSelectList.indexOf(JSON.stringify(item));
      let course = {};
      if (currentSelectList.some((subItem) => JSON.parse(subItem).id === item.id)) {
        course = {
          status: true,
          text: item.courseStatus === COURSE_STATUS.FULL_BOOKED ? '' : '',
          class: item.courseStatus === COURSE_STATUS.FULL_BOOKED ? styles.cannot : styles.already,
          id: item.id
        };
      } else {
        course = this.getCourseStatus(item);
      }

      // 
      if (
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V2 ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V4 ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_SPECIAL ||
        course.courseStatus === COURSE_STATUS.ALREADY_FINISH
      ) {
        this.surplusCourse = this.surplusCourse - 1;
      }

      // 
      if (happyNewYear) {
        course = {
          ...course,
          courseStatus: COURSE_STATUS.HAPPY_NEW_YEAR, // 
          text: '', // 
          status: false // 
        };
      }
      return (
        <li
          key={`${currentSelect}${item.id}`}
          onClick={course.status ? this.makeAppointment.bind(null, item) : null}
          className={className(course.class, happyNewYear && styles['happy-new-year'])}
        >
          <p className={styles.timer}>{item.beginTime}</p>
          <p className={styles.status}>{course.text}</p>
        </li>
      );
    });
  };

  /**
   * 
   */
  getCourseStatus = (currentCourse) => {
    let course = {
      status: true,
      text: '',
      class: styles.cannot,
      courseStatus: currentCourse.courseStatus,
      id: currentCourse.id
    };
    switch (currentCourse.courseStatus) {
      // 
      case COURSE_STATUS.ALREADY_FINISH:
        course = {
          status: false,
          text: '',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 
      case COURSE_STATUS.ALREADY_BOOKED:
        course = {
          status: false,
          text: '',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 
      case COURSE_STATUS.ALREADY_BOOKED_SPECIAL:
        course = {
          status: false,
          text: '',
          class: styles.cannot,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 1v4
      case COURSE_STATUS.ALREADY_BOOKED_1V4:
        course = {
          status: false,
          text: '4',
          class: this.getBookedStatueStyle(ClassType.MAIN_1v4),
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 1v2
      case COURSE_STATUS.ALREADY_BOOKED_1V2:
        course = {
          status: false,
          text: '2',
          class: this.getBookedStatueStyle(ClassType.MAIN_1v2),
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 
      case COURSE_STATUS.FULL_BOOKED:
        course = {
          status: false,
          text: '',
          class: styles.cannot,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 
      case COURSE_STATUS.NORMAL:
        course = {
          status: true,
          text: '',
          class: '',
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
    }
    return course;
  };

  getBookedStatueStyle = (type) => {
    const { classType } = this.props;
    if (classType !== type) {
      return styles.cannot;
    }
    return styles.success;
  };

  /**
   *
   */
  getCurrentWeek = (date) => {
    const nowWeek = moment().format('YYYY-MM-DD');
    if (nowWeek === date) return '';
    let sWeek = moment(date).format('dddd');
    switch (sWeek) {
      case 'Monday':
        sWeek = '';
        break;
      case 'Tuesday':
        sWeek = '';
        break;
      case 'Wednesday':
        sWeek = '';
        break;
      case 'Thursday':
        sWeek = '';
        break;
      case 'Friday':
        sWeek = '';
        break;
      case 'Saturday':
        sWeek = '';
        break;
      case 'Sunday':
        sWeek = '';
        break;
      default:
        break;
    }
    return sWeek;
  };

  /**
   *
   */
  handleRefresh = () => {
    const url = window.location.href;
    window.location.replace(url);
  };

  /**
   * @description 
   */
  handleAfterChange = (index) => {
    const { daysData } = this.state;
    if (index > 0) {
      this.handleSelect(daysData[index]);
    } else {
      this.handleSelect(moment().format('YYYY-MM-DD'));
    }
  };

  render() {
    const { refresh, classType } = this.state;
    const { style } = this.props;

    if (refresh) {
      return (
        <div className={styles.classContent}>
          <div className={styles.nowifiwrapper}>
            <div className={styles.nowifibox} />
            <div className={styles.nowifitext}></div>
            <div className={styles.nowifirefreshbutton} onClick={this.handleRefresh}>
              
            </div>
          </div>
        </div>
      );
    }

    return (
      <div key={classType} style={style} className={styles.classContent}>
        <div className={styles.optional}>
          <div className={styles.slideOuter}>
            <div className={styles.slideInner}>
              <ul className={styles.slide}>
                <Slider
                  ref={(slider) => {
                    this.dateSlider = slider;
                  }}
                  {...DateSettings}
                  afterChange={this.handleAfterChange}
                  key={classType}
                >
                  {this.renderDateView()}
                </Slider>
              </ul>
            </div>
          </div>
        </div>
        <div className={styles.duration}>
          <span>25</span>
          {classType === ClassType.MAIN_1v2 && (
            <u onClick={this.props.handleSetModalKey.bind(null, 'notice')}></u>
          )}
        </div>
        <div className={styles.container}>{this.renderCourseView(classType)}</div>
      </div>
    );
  }
}

export default ClassList;
