import React from 'react';
import {Toast, Modal} from 'antd-mobile';
import rabbitSrc from '../public/images/rabbit.png';

/**
 *  Toast
 * /
 *  Icon  Toast 4-6  Icon  Toast 14 
 *
 * Toast.info(content, duration, onClose, mask)
 * duration = 0 onClose toast  toast  hide
 */

const alert = Modal.alert;

// /**
//  * , 
//  */
// let confirmInstance = null;

/**
 * Logo
 */
const rabbit = (
    <div className="modal-rabbit-wrapper">
        <img src={rabbitSrc} style={{width: '100%'}} className="modal-rabbit" alt=""/>
    </div>
);

const message = {
    /**
     *  
     */
    info: (content, callback) => {
        Toast.info(content, 1.5, callback);
    },
    /**
     *  
     */
    success: (content, callback) => {
        Toast.success(content, 1.5, callback);
    },
    /**
     *  
     */
    error: (content, callback) => {
        Toast.info(content, 1.5, callback);
    },
    /**
     *  
     */
    offline: (content, callback) => {
        Toast.offline(content, 1.5, callback);
    },

    /**
     * 
     * @param message 
     * @param okFun 
     * @param okText 
     * @param cancelFun 
     * @param cancelText 
     *
     * am-modal-content
     */
    confirm: ({message, okFun, okText = '', cancelFun, cancelText = ''}) => {
        // confirmInstance =
        alert(rabbit, message, [
            {
                text: cancelText, onPress: () => {
                    cancelFun && cancelFun();
                },
                style: 'default'
            },
            {
                text: okText, onPress: () => {
                    okFun && okFun();
                }
            },
        ]);
    },
    /**
     *  Loading, 
     */
    showLoading: (content = '') => {
        Toast.loading(content, 0);
    },
    /**
     *  Loading
     */
    closeLoading: () => {
        Toast.hide();
    },
};

export default message;
