import {BUTTON_TEXT, MODAL_KEY, RECOMMEND_JUDGE} from '../../constants/special';
import {specialService} from '../../service';
import {message} from '../../utils';
import {appConfig} from '../../config';


const basename = appConfig.basename;

/**
 * @description  modalKey
 */
export function handleJumpTo(spId) {
    const {buttonText, buttonUrl} = this.state;

    if (buttonText === BUTTON_TEXT.BOOKING_BTN) {
        // 
        specialService.getSepcialStudentStatus(spId).then(res => {
            if (!res.EXIST_JUDGE) {
                //  
                this.setState({
                    modalKey: MODAL_KEY.BIRTHDAY,
                    currentspId: spId,
                });
            } else if (res.AGE_SATISFY_JUDGE) {
                //   
                window.location.href = basename + `/special_booking?spId=${spId}`;
            } else if (!res.AGE_SATISFY_JUDGE) {
                //       
                specialService.getRecommendCourse(spId).then(res => {
                    if (!res.spCourseId) {
                        this.setState({
                            modalKey: MODAL_KEY.NO_MATCH_CLASS,
                        });
                    } else {
                        this.setState({
                            modalKey: MODAL_KEY.RECOMMEND_CLASS,
                            judgeText: RECOMMEND_JUDGE[res.judgeEnum],
                            recommend_spid: res.spCourseId,
                            recommendAgeLevel: res.ageLevel,
                        });
                    }
                }).catch(e => {
                    message.error(e.msg);
                });
            }
        });
    } else {
        if (buttonUrl) {
            // 
            window.location.href = buttonUrl;
        } else {
            //  
            this.setState({
                modalKey: MODAL_KEY.SPECIAL_CLASS_END
            });
        }
    }
}

/**
 * @description 
 * @param year
 * @param month
 */
export function handleUpdateBirthDay(year, month) {
    if (!year || !month) {
        return message.error('');
    }
    const {currentspId} = this.state;
    specialService.updateBirthday(year, month).then(() => {
        specialService.getSepcialStudentStatus(currentspId).then(res => {
            if (res.AGE_SATISFY_JUDGE) {
                //   
                window.location.href = basename + `/special_booking?spId=${currentspId}`;
            } else if (!res.AGE_SATISFY_JUDGE) {
                //       
                specialService.getRecommendCourse(currentspId).then(res => {
                    if (!res.spCourseId) {
                        this.setState({
                            modalKey: MODAL_KEY.NO_MATCH_CLASS,
                        });
                    } else {
                        this.setState({
                            modalKey: MODAL_KEY.RECOMMEND_CLASS,
                            judgeText: RECOMMEND_JUDGE[res.judgeEnum],
                            recommend_spid: res.spCourseId,
                            recommendAgeLevel: res.ageLevel,
                        });
                    }
                }).catch(e => {
                    message.error(e.msg);
                });
            }
        });
    }).catch((e) => {
        message.error(e.msg);
    });
}


/**
 * @description 
 * @param spId
 */
export function handleJumpToSpecialDetail(spId) {
    window.location.href = basename + `/special_detail/${spId}`;
}
