import { PureComponent } from 'react';
import PropTypes from 'prop-types';

import { wechatService } from '../../service';
import { wechatUtils, tools } from '../../utils';
import { appConfig } from '../../config';

/**
 * 
 * , JS-SDK, 
 */
class WechatConfig extends PureComponent {
  static propTypes = {
    showShare: PropTypes.bool.isRequired, // 
    shareOptions: PropTypes.object //   {title, desc, link, imgUrl, type, dataUrl}
  };

  static defaultProps = {
    showShare: false,
    shareOptions: {}
  };

  componentDidMount() {
    this.initData();
  }

  /**
   * , , 
   */
  initData = async () => {
    // 
    if (appConfig.env === 'development' || appConfig.env === 'dev') return;

    // , JS-SDK
    if (!tools.isWeChat()) return;

    const { showShare, shareOptions } = this.props;

    const ready = wechatUtils.wechatStatus.ready;
    // ,  IOS
    if (tools.isAndroid() || !ready) {
      wechatService
        .getWechatSignature()
        .then((wechatSign) => {
          wechatUtils.wechatConfig(wechatSign, showShare, shareOptions);
        })
        .catch((error) => {
          console.error('WechatConfig >> ', error);
        });
    } else {
      // IOS
      wechatUtils.configShare(showShare, shareOptions);
    }
  };

  render() {
    return null;
  }
}

export default WechatConfig;
