import React from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import classNames from 'classnames';

import {message} from '../../utils';


import styles from './index.less';

/**
 * @description 
 */
export default class CpModal extends React.Component {
    state = {
        textIndex: 0,
        list: [
            `14`,
            `1`,
            ``,
            ``,
            `~`,
            `1114iPad`,
            `,~`,
            ``,
            ``,
            `809814`,
        ]
    };

    /**
     * @description 
     */
    handleChangeIndex = () => {
        const {textIndex, list} = this.state;
        let len = list.length;
        let next;
        if (textIndex + 1 >= len) {
            next = 0;
        } else {
            next = textIndex + 1;
        }
        this.setState({
            textIndex: next
        });
    };

    renderCopyButton = () => {
        const {list, textIndex} = this.state;
        return (
            <div>
                <CopyToClipboard
                    text={list[textIndex].replace(/<br\/>/g, '\n')}
                    onCopy={() => message.info('!')}
                >
                    <button className={classNames(styles.makeBtn, styles.copy)}></button>
                </CopyToClipboard>
            </div>
        );
    }

    render() {
        const {list, textIndex} = this.state;
        const {onCancel} = this.props;
        return (
            <div className={styles['cp-modal']}>
                <div className={styles.mask} onClick={onCancel}/>
                <div className={styles.card}>
                    <div className={styles['body-bg']}/>
                    <div className={styles.body}>
                        <p className={styles.title}>~</p>
                        <p className={styles.content} dangerouslySetInnerHTML={{__html: list[textIndex]}}>
                        </p>
                        <div className={styles.refresh} onClick={this.handleChangeIndex}>
                            
                        </div>
                    </div>
                    <div className={styles.mailer}/>
                </div>
                {this.renderCopyButton()}
            </div>
        );
    }
}
