import { fetchUtils, tools } from '../utils';
import { AccountsType } from '../constants';
import { appConfig } from '../config';

const mobileDomain = appConfig.domain.mobileDomain;

//  +, IOS SPA bug
const entryUrl = window.location.pathname + window.location.search;

/**
 * URL, 
 * @returns {*|Promise<T>}
 */
export const getWechatSignature = () => {
  const url = mobileDomain.concat('/wx/newjsapi/signature');
  const data = {
    // urlurl +
    url: tools.isIOS() ? entryUrl : window.location.pathname + window.location.search
  };
  return fetchUtils.get(url, data, { includeToken: false }).then((res) => {
    return res.data;
  });
};

/**
 * code, openId  token
 *  cookie, "wxToken"
 * @param code
 * @returns {*|Promise<any>}
 */
export const getOpenIdByCode = (code) => {
  const url = mobileDomain.concat('/wx/asyncOauth2');
  const data = {
    code
  };

  return fetchUtils.get(url, data, { includeToken: false, jumpSuccessCode: true });
};

/**
 * codeaccount, openId  token
 * @param code
 * @param account
 * @returns {*|Promise<any>}
 */
export const getOpenIdByCodeAndAccount = (code, account) => {
  let url;
  switch (account) {
    case AccountsType.MAIN:
      url = mobileDomain.concat('/wx/asyncOauth2');
      break;
    case AccountsType.ACTIVITY:
      url = mobileDomain.concat('/wx99/asyncOauth2');
      break;
    default:
      url = mobileDomain.concat('/wx/asyncOauth2');
      break;
  }

  const data = {
    code
  };

  return fetchUtils.get(url, data, { includeToken: false, jumpSuccessCode: true });
};
