import RequestUtils, {
  IAnyValueObject,
  IRequestConfig,
  IRequestGlobalConfig
} from '@mmears/request';
import logHelper from '@mmears/log';

import { RESULT_CODE } from '../constants';
import { appConfig } from '../config';
import * as localStore from './local-store';
import queryString from 'query-string';

// 
const basename = appConfig.basename;

/**
 * WebView, : X-WECHAT-TOKEN
 */
const getHeadersWhenInWebView = () => {
  // miniWxToken WebView, 
  // @ts-ignore
  const { miniWxToken } = queryString.parse(location.search) || {};

  if (!miniWxToken) {
    return {};
  }
  // wxToken X-WECHAT-TOKEN
  return {
    'X-WECHAT-TOKEN': miniWxToken
  };
};

/**
 * 
 */
export interface IRequestConfigV2 extends IRequestConfig {
  /**
   * token
   */
  includeToken?: boolean;
}

/**
 * 
 */
const logger = logHelper({
  eventName: 'web_log',
  showLog: false, // process.env.NODE_ENV === 'development'
  getAccount: () => {
    return localStore.getUserId() || '';
  }
});

/**
 *   
 */
const config: IRequestGlobalConfig = {
  /**
   *  8
   */
  timeout: 8000,
  /**
   * ,  'OK'
   */
  successCode: 'OK',
  /**
   * cookie
   */
  withCredentials: true,
  logger,
  trackInOut: false, // 
  perfRule: 'all', //  , 2 
  /**
   * 
   */
  headersHandle: () => {
    let headers = {
      'X-USER-TOKEN': localStore.getToken() // token
    };

    // if (process.env.NODE_ENV === 'development') {
    //   headers[
    //     'X-USER-TOKEN'
    //   ] = `m-zhh_j3tvKCnHcC7t8N8GpXR7ibiBoYCA9BMsDfKug4hOCPR6AP1btf5c5Blc1V0JZeZxy5uaoSFyNka1vPIhYDn1_6Uw3gCF60Ei6HPeYlJDQPvCttFJ4dkkKjKFC1MRJMG2513SMtguvV9faiwg`;
    // }

    //  H5, wxToken, 
    const wechatToken = localStore.getWechatToken();
    if (wechatToken) {
      // @ts-ignore
      headers['X-WECHAT-TOKEN'] = wechatToken;
    }

    const miniWxToken = getHeadersWhenInWebView();
    if (miniWxToken) {
      headers = {
        ...headers,
        ...miniWxToken
      };
    }

    // operateType ,   or , , 
    const operateType = localStore.get('operateType');
    if (operateType) {
      // @ts-ignore
      headers['operateType'] = operateType;
    }

    return headers;
  },
  errorHandle: (error) => {
    // 
    switch (error.code) {
      case RESULT_CODE.TOKEN_NULL:
      case RESULT_CODE.TOKEN_EXPIRED:
      case RESULT_CODE.TOKEN_ILLEGAL:
      case RESULT_CODE.LOGIN_REQUIRED:
        localStore.clearToken();
        // @ts-ignore
        const { rf } = queryString.parse(location.search) || {};
        let redirectUrl = `${basename}/login?callback=${encodeURIComponent(window.location.href)}`;
        // rf
        if (rf) {
          redirectUrl += `&rf=${rf}`;
        }
        window.location.replace(redirectUrl);
        return Promise.reject(error);
      default:
        return Promise.reject(error);
    }
  }
};

/**
 * 
 */
const request = new RequestUtils(config);

/**
 *   
 * @param config
 */
const convertConfig = (config?: IRequestConfigV2): IRequestConfigV2 | undefined => {
  // token,  X-USER-TOKEN
  if (config && config.includeToken === false) {
    config.headers = config.headers || {};
    config.headers['X-USER-TOKEN'] = '';
  }
  return config;
};

/**
 * 
 * @returns {*}
 */
const fetchUtils = {
  /**
   * 
   * @param url url
   * @param params queryString 
   * @param config 
   */
  get(url: string, params?: IAnyValueObject, config?: IRequestConfigV2) {
    return request.get(url, params, convertConfig(config));
  },
  /**
   * 
   * @param url
   * @param data
   * @param config
   */
  post(url: string, data?: any, config?: IRequestConfigV2) {
    return request.post(url, data, convertConfig(config));
  },
  /**
   * 
   * @param url
   * @param data
   * @returns {Promise<any>}
   */
  postJson(url: string, data?: any, config?: IRequestConfigV2) {
    return request.postJson(url, data, convertConfig(config));
  }
};

export default fetchUtils;
